        <!--R01-->
        <el-dialog :visible.sync="Dialog_R01">
          <span>
            <div class="heading_title">
              SPORTLER KÄSEKUCHEN
            </div>
          </span>
          <span>
            <el-row
              gutter="60"
              type="flex"
              justify="center"
            >
              <el-col span="4">
                <el-avatar
                  :size="120"
                  fit="fit"
                  src="https://i.bmp.ovh/imgs/2020/04/75c6542f7159581c.png"
                ></el-avatar>
              </el-col>
              <el-col span="20">
                <div
                  class="heading_name"
                  style="margin-bottom:10px"
                >
                  Andre Duda
                </div>
                <el-card
                  class="card_chat"
                  shadow="never"
                >
                  <div class="text_content">
                    <div>
                      Hallo liebe Angelique Kerber ! Ich habe für Dich ein
                      Rezept herausgesucht, was zu dir als Sportlerin passt und
                      du auch genießen kannst in dieser, leider erzwungenen,
                      tennisfreien Zeit ! Und zwar einen SPORTLER KÄSEKUCHEN !
                      Dieser ist arm an Kohlehydraten und ein ideales Gericht
                      der Low Carb Ernährungsform ! Ich wünsche Dir sportlichen
                      Genuß und guten Appetit mit Deiner Familie und deinen
                      Freunden liebe Angie ! Alles Gute, bleib gesund, pass auf
                      dich auf und wir Fans hoffen dich bald wieder auf den
                      Courts dieser Welt zu sehen und das bald das geplante
                      Gruppentreffen von uns mit dir stattfinden kann ! Liebe
                      Grüße von deinem Fan Andre Duda
                    </div>
                  </div>
                </el-card>
              </el-col>
            </el-row>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont shucai"></i>Zutaten
            </div>
            <el-card
              class="card_tag"
              shadow="never"
            >4Stk. Eier</el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >250g Frischkäse</el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >500g Magertopfen (Magerquark)</el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >50g Proteinpulver</el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1Spr. Vanillearoma</el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >15g Süßstoff</el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1Schuss Rum</el-card>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont distance"></i>Zeit
            </div>
            <div>
              <el-card
                class="card_time"
                shadow="never"
              >60 min. Gesamtzeit</el-card>
              <el-card
                class="card_time"
                shadow="never"
              >15 min. Zubereitungszeit</el-card>
              <el-card
                class="card_time"
                shadow="never"
              >45 min. Kochzeit (Backzeit)</el-card>
            </div>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont cook"></i>Zubereitung
            </div>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-1"></i>Heizen Sie den Backofen auf 200
                Grad Ober-Unterhitze vor. Trennen Sie die Eier in Dotter und
                Eiklar und schlagen das Eiklar zu steifem Schnee. Nun verrühren
                Sie den Topfen und den Frischkäse mit den Eidottern, dem Rum und
                dem Süßstoff, am Besten mit dem Handmixer.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-2"></i>Nun rühren Sie das Proteinpulver
                sowie das Vanillearoma in die Masse und heben zum Schluss den
                Eischnee vorsichtig unter.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-3"></i>Nun wird die Käsekuchen-Masse in
                eine Silikonform gefüllt und im Backofen bei 180 Grad für etwa
                45 Minuten gebacken.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-4"></i>Der Kuchen schmeckt am besten,
                wenn er vollständig ausgekühlt ist und kalt serviert wird!
              </div>
            </el-card>
          </span>
        </el-dialog>
        <!--R02-->
        <el-dialog :visible.sync="Dialog_R02">
          <span>
            <div class="heading_title">
              Käsekuchen
            </div>
          </span>
          <span>
            <el-row
              gutter="60"
              type="flex"
              justify="center"
            >
              <el-col span="4">
                <el-avatar
                  :size="120"
                  fit="fit"
                  src="https://i.bmp.ovh/imgs/2020/04/aca24460d8e7ae3a.png"
                ></el-avatar>
              </el-col>
              <el-col span="20">
                <div
                  class="heading_name"
                  style="margin-bottom:10px"
                >
                  Denny Dinauer
                </div>
                <el-card
                  class="card_chat"
                  shadow="never"
                >
                  <div class="text_content">
                    <div>
                      Ich habe für das Kochbuch, auch mal für zwischendurch ein Rezept für einen tollen Käsekuchen
                    </div>
                    <div>Viele Grüße</div>
                    <div>Denny Dinauer
                    </div>
                  </div>
                </el-card>
              </el-col>
            </el-row>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont shucai"></i>Zutaten
            </div>
            <div class="heading_subtitle">Für den Teig</div>
            <el-card
              class="card_tag"
              shadow="never"
            >150 g Mehl
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >65 g Zucker
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >65 g Butter
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 Ei(er)
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 TL Backpulver
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >etwas Salz
            </el-card>
            <div class="heading_subtitle">Für den Baiserbelag
            </div>
            <el-card
              class="card_tag"
              shadow="never"
            >3 Eiweiß
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >100 g Zucker
            </el-card>
            <div class="heading_subtitle">Für den Belag
            </div>
            <el-card
              class="card_tag"
              shadow="never"
            >750 g Quark (Magerquark)
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >150 g Zucker
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 ½ Pck. Puddingpulver (Vanille)
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >500 ml Milch
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >3 Eigelb
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 Pck. Vanillezucker
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >½ Zitrone(n), den Saft davon
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 Tasse Öl
            </el-card>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont cook"></i>Zubereitung
            </div>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-1"></i> Alle Zutaten für den Teig zu einem Knetteig verarbeiten. In einer gefetteten Springform (26 cm) verteilen und einen kleinen Rand hoch drücken. Alle Zutaten für den Belag verrühren und 45 Minuten bei 200°C backen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-2"></i> Für das Baiser das Eiweiß steif schlagen und den Zucker dabei einrieseln lassen. Auf die Käsefüllung streichen, dabei 1 cm Rand lassen. Die Masse mit einer Stricknadel durchstechen und noch mal 20 Minuten bei 160°C backen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-3"></i> Den Springformrand erst lösen, wenn der Kuchen gut abgekühlt ist. Die Pudding-Käsemasse fließt sonst von dannen. Die Tränchen auf der Baisermasse kommen mit der Zeit ganz automatisch, lasst euch überraschen. Der Kuchen lässt sich wunderbar schon einen Tag vorher zubereiten.
              </div>
            </el-card>
          </span>
        </el-dialog>
        <!--R03-->
