            >
              <div>
                <i class="iconfont icon-2"></i> Die Himbeeren abwechselnd mit der Mascarpone-Creme in eine Glasschüssel oder portionsweise in Gläsern schichten. Letzte Schicht sollte die Mascarpone-Creme sein. Die Blockschokolade mit einem Sparschäler fein hobeln. Die Creme damit bestreuen. Ca. 1 Stunde kühl stellen
              </div>
            </el-card>
          </span>
