            >
              <el-col span="4">
                <el-avatar
                  :size="120"
                  fit="fit"
                  src="https://i.postimg.cc/MZfHF30q/image.png"
                ></el-avatar>
              </el-col>
              <el-col span="20">
                <div
                  class="heading_name"
                  style="margin-bottom:10px"
                >
                  Beth Wilson
                </div>
                <el-card
                  class="card_chat"
                  shadow="never"
                >
                  <div class="text_content">
                    <div>
                      Hey Angie! Hoping you enjoy this gift from all of your loyal fans on your FB fanpage. You mentioned that you currently enjoy cooking while in iso so hopefully all these different recipes keep you busy. I’ve included a classic Christmas dessert that I make every year for my family which they love! Hope you do aswell🤗i would like to thankyou for all the incredible memories from January 2020 in Adelaide & Melbourne! I absolutely love supporting you & really hope I can make it over to Stuttgart next April to support you at one of the best tournaments on the calendar! Stay safe Angie & enjoy the book. Much love to you. Beth x🤗
