                <el-image
                  style="width:100%"
                  src="https://ftp.bmp.ovh/imgs/2020/04/325805ac055f8cad.jpg"
                ></el-image>
              </el-card>
            </div>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont shucai"></i>Zutaten
            </div>
            <el-card
              class="card_tag"
              shadow="never"
            >1 1/4 cups (200g) chopped dates
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 1/4 (310ml) water
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 teaspoon bicarbonate soda
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >60g butter
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >3/4 cup (165g) caster sugar
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >2 eggs
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 cup (150g) self- raising flour
            </el-card>
            <div class="heading_subtitle">Butterscotch sauce</div>
            <el-card
              class="card_tag"
              shadow="never"
            >1 1/2 cups (300g) firmly packed brown sugar
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >250g unsalted butter, chopped
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >300ml thickened cream
            </el-card>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont cook"></i>Zubereitung
            </div>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-1"></i> grease deep 19cm square cake pan & line base with baking paper.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-2"></i> combine dates and water in pan, bring to boil.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-3"></i> remove dates and water from heat and add soda, stand for 5 minutes.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-4"></i> beat butter and sugar in a small mixing bowl with electric mixer until combined.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-5"></i> beat in eggs 1 at a time, beat until combined.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-6"></i> gently whisk in sifted flour then the date mixture.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-7"></i> pour mixture into prepared pan and bake in a moderate oven (approx 180 degrees) for 50 minutes.
              </div>
            </el-card>
            <div class="heading_subtitle">Butterscotch sauce method</div>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-1"></i> combine all ingredients in a saucepan and stir over medium heat without boiling.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-2"></i> when sugar is dissolved simmer stirring for 5 minutes.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-3"></i> serve with vanilla ice cream or a dollop of cream.
              </div>
            </el-card>
          </span>
        </el-dialog>
        <!--R05-->
        <el-dialog :visible.sync="Dialog_R05">
          <span>
            <div class="heading_title">
              Käse-Lauch-Suppe
            </div>
          </span>
          <span>
            <el-row
              gutter="60"
              type="flex"
              justify="center"
            >
              <el-col span="4">
                <el-avatar
                  :size="120"
                  fit="fit"
                  src="https://i.postimg.cc/XJfFYPvf/image.png"
                ></el-avatar>
              </el-col>
              <el-col span="20">
                <div
                  class="heading_name"
                  style="margin-bottom:10px"
                >
                  Mara Hesse
                </div>
                <el-card
                  class="card_chat"
                  shadow="never"
                >
                  <div class="text_content">
                    <div>
                      Liebe Grüße Mara Hesse aus Wulften am Harz
                    </div>
                  </div>
                </el-card>
              </el-col>
            </el-row>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont shucai"></i>Zutaten
            </div>
            <el-card
              class="card_tag"
              shadow="never"
            >500 g Hackfleisch, gemischt
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >3 Stange/n Lauch
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >250 g Schmelzkäse
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 Becher Crème fraîche, ca. 150 g
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >3 Würfel Gemüsebrühe
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >700 ml Wasser
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >2 kleine Baguette(s) zum Aufbacken
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Salz und Pfeffer, aus der Mühle
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >3 EL Öl
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Muskat
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Knoblauchpulver
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Zwiebelpulver
            </el-card>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont cook"></i>Zubereitung
            </div>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-1"></i> Den Ofen auf 175 °C Ober-/Unterhitze vorheizen. Die Baguettes in den vorgeheizten Ofen legen und ca. 10 Minuten backen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-2"></i> Derweil Öl in einen großen Topf geben. Das Hackfleisch darin von allen Seiten gut anbraten und mit Salz und Pfeffer würzen. Den Lauch in kleine Ringe schneiden und zum Hackfleisch geben. Ca. 5 Minuten mit anbraten.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-3"></i>Das Wasser zugießen, Brühwürfel hineingeben und alles ca. 10 Minuten auf kleiner Flamme köcheln lassen. Den Schmelzkäse einrühren und schmelzen lassen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-4"></i>Crème fraîche einrühren und noch einmal kurz aufkochen lassen. Die Suppe mit Salz, Pfeffer, Muskat, Knoblauch und Zwiebelpulver kräftig abschmecken.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-5"></i> Die Baguettes in Scheiben schneiden und zu der Suppe reichen.
              </div>
            </el-card>
          </span>
        </el-dialog>
        <!--R06-->
        <el-dialog :visible.sync="Dialog_R06">
          <span>
            <div class="heading_title">
              Falsches Hirn
            </div>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont shucai"></i>Zutaten
            </div>
            <el-card
              class="card_tag"
              shadow="never"
            >4 Eier
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Salz Pfeffer
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >etwas Chili Pulver
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 kleine rote Zwiebel
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 kleine Paprika gelb
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >3 mittelgroße saure Gurken
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >3 Champignons
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1/4 Pfund Feischwurst
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Gurkenbrühe
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Etwas öl oder Pflanzenfett
            </el-card>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont cook"></i>Zubereitung
            </div>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-1"></i> Zutaten klein schneiden,
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-2"></i> Eier gut verrühren,extra etwas salz zugeben,
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-3"></i> fett in Pfanne erhitzen
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-4"></i> Zwiebel,Paprika,saure Gurken Champignons und Wurst
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-5"></i> gewürze zugeben und immer rühren,
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-6"></i> nach ca.2bis 3 Min.Eier unterrühren.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-7"></i> Zum Schluß wird die Gurkenbrühe dazu gegossen und immer rühren.dann sieht es aus wie Hirn.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-8"></i>Als beilage kann man Brot dazu Essen. Guten Apetit.
              </div>
            </el-card>
          </span>
        </el-dialog>
        <!--R07-->
        <el-dialog :visible.sync="Dialog_R07">
          <span>
            <div class="heading_title">
              Orecchiette pasta with turnip green
            </div>
          </span>
          <span>
            <el-row
              gutter="60"
              type="flex"
              justify="center"
            >
              <el-col span="4">
                <el-avatar
                  :size="120"
                  fit="fit"
                >
                  <div class="avatar_text">AS</div>
                </el-avatar>
              </el-col>
              <el-col span="20">
                <div
                  class="heading_name"
                  style="margin-bottom:10px"
                >
                  Armando Soave
                </div>
                <el-card
                  class="card_chat"
                  shadow="never"
                >
                  <div class="text_content">
                    <div>
                      The Orecchiette with turnip green, is a traditional recipe from the Puglia region and Taranto City, and the most famous dish of the Apulian cuisine, maybe! We Apulians call it "Orecchiette alle cime di rapa"
                    </div>
                  </div>
                </el-card>
              </el-col>
            </el-row>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont camera"></i>Foto</div>
            <div>
              <el-card>
                <el-image
                  style="width:100%"
                  src="https://ftp.bmp.ovh/imgs/2020/04/d217bedbf4591ca9.jpg"
                ></el-image>
              </el-card>
            </div>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont shucai"></i>Zutaten
            </div>
            <div class="heading_subtitle">For two people
            </div>
            <el-card
              class="card_tag"
              shadow="never"
            >300 gr of orecchiette pasta(fresh pasta)
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >250 gr of turnip greens
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 garlic cloves
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >half red chilli
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >extra virgin olive oil (Evo Olive Oil)
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >salt
            </el-card>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont cook"></i>Zubereitung
            </div>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-1"></i> Wash turnip green under lukewarm water and then remove stems.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-2"></i> Put some water in a big pot, add the salt, and let it boil, as soon as the water starts to boil, put the orecchiette pasta ans green turnips in the water, and cook
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-3"></i> In a aluminum or non-stick pan, add some evo oli(the amount about 3 or 4 spoons), the garlic cloves that have been peeled and crushed, the chili peppers that have been broken into pieces. Turn off the heat when the garlic begins to brown.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-4"></i> Drain le orecchiette e turnip green, when they are still undercooked, very al dente.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-5"></i> Turn the heat back up high under the pan with the evo oil, garlic cloves and chili peppers, and add the orecchiette and turnip green. so Stir continuously until all ingredients have fully cooked and have been flavored by the oil.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-6"></i> remove the garlic clove, and serve at the dinner table
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-7"></i> Optional note:To personal taste, in point 3 you can add some anchovies in oil.
              </div>
            </el-card>
          </span>
        </el-dialog>
        <!--R08-->
        <el-dialog :visible.sync="Dialog_R08">
          <span>
            <div class="heading_title">
              KLASSISCHES JÄGERSCHNITZEL
            </div>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont shucai"></i>Zutaten
            </div>
            <el-card
              class="card_tag"
              shadow="never"
            >750g frische Champignons
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >50g Bacon
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >2 klein gehackte Zwiebeln
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >250ml Gemüsebrühe
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >250ml Sahne
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1TL getrockneter Thymian
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 Bund Petersilie
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >½ TL Salz
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Pfeffer
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >n.B. Milch
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >4 Schweineschnitzel (Oberschale)
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Mehl
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1Eigelb
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Semmelbrösel
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Butterschmalz
            </el-card>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont cook"></i>Zubereitung
            </div>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-1"></i> Zuerst die Schnitzel waschen, gut trocken tupfen und dünn klopfen. Mit Salz und Pfeffer großzügig würzen. Dann in Mehl, geschlagenen Eigelb und Paniermehl wenden und in einer Pfanne von beiden Seiten im Butterschmalz goldgelb braten. Anschließend die Schnitzel aus der Pfanne nehmen und warm stellen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-2"></i> Wenn nötig wieder etwas Fett in die Pfanne geben und die klein gehackte Zwiebel anbraten. Wenn die Zwiebeln leicht bräunen, die Champignons wieder zugeben und mit der Brühe und der süßen Sahne auffüllen. Salzen und Pfeffern und den getrockneten Thymian dazugeben. Die Sauce jetzt unter gelegentlichem Rühren so lange einköcheln, bis die Flüssigkeit deutlich weggekocht ist. (Das kann je nach Temperatur bis zu 15 Minuten dauern).
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-3"></i> Dann unter Rühren so viel Milch zugeben, bis die Sauce die gewünschte cremige Konsistenz erreicht hat. Die Sauce sollte nicht zu dünn sein und eine rahmige Konsistenz haben. Die Pfanne vom Herd nehmen und ¾ der gehackten Petersilie unter die Sauce heben. Eventuell noch mit Salz und Pfeffer nachwürzen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-4"></i> Jetzt die Schnitzel auf die Teller geben und mit der Sauce übergießen. Die verbliebene Petersilie zur Dekoration über die Sauce geben.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-5"></i> Dazu passen sehr gut Pommes Frites oder Kartoffelkroketten.
              </div>
            </el-card>
          </span>
        </el-dialog>
        <!--R09-->
        <el-dialog :visible.sync="Dialog_R09">
          <span>
            <div class="heading_title">
              The Brazen Head
            </div>
          </span>
          <span>
