        <!--R10-->
        <el-dialog :visible.sync="Dialog_R10">
          <span>
            <div class="heading_title">
              Murzynek
            </div>
          </span>
          <span>
            <el-row
              gutter="60"
              type="flex"
              justify="center"
            >
              <el-col span="4">
                <el-avatar
                  :size="120"
                  fit="fit"
                  src="https://i.bmp.ovh/imgs/2020/04/e9a36e1e9855c8a9.png"
                ></el-avatar>
              </el-col>
              <el-col span="20">
                <div
                  class="heading_name"
                  style="margin-bottom:10px"
                >
                  Ellen
                </div>
