        <!--R11-->
        <el-dialog :visible.sync="Dialog_R11">
          <span>
            <div class="heading_title">
              Lasagne ala Uroma
            </div>
          </span>
          <span>
            <el-row
              gutter="60"
              type="flex"
              justify="center"
            >
              <el-col span="4">
                <el-avatar
                  :size="120"
                  fit="fit"
                  src="https://i.postimg.cc/7L0yp4p7/image.png"
                ></el-avatar>
              </el-col>
              <el-col span="20">
                <div
                  class="heading_name"
                  style="margin-bottom:10px"
                >
                  Diana Hohlbein
                </div>
                <el-card
                  class="card_chat"
                  shadow="never"
                >
                  <div class="text_content">
                    <div>
                      Liebe Angie,</div>
                    <div>erstmal ganz lieben Dank für dein tolles Video, in dem du einige unserer Fragen beantwortet hast. Dadurch entstand die Idee, dir einige unserer Lieblingsrezepte oder Restaurants zu schicken, da du durch mehr Zeit das Kochen für dich entdeckt hast 😊. Ich stelle mich in der Küche oft nicht sehr geschickt an, aber die Lasagne beherrsche ich richtig gut. Leider also kein wirklich gesundes Rezept, aber du bist so fit, das steckst du locker weg 😉.</div>
                    <div>Ich freue mich sehr, dass es dir gut geht 😊! In deinem Video machst du einen sehr glücklichen und ausgeglichenen Eindruck, obwohl dir der Tennis-Sport mindestens genau so fehlt wie uns allen – sowohl zum Anschauen als auch zum selber Spielen. </div>
                    <div>Eigentlich wären wir am Sonntag wieder mit der Kerberazzi-Gruppe nach Stuttgart gefahren (inkl. meinem Papa, der es fast jedes Jahr schafft, uns verrückten Haufen auszuhalten), um dich, Petko und die anderen deutschen Mädels anzufeuern. Nun wird einem noch viel mehr bewusst, wie besonders diese Woche im Jahr für uns ist.</div>
                    <div>Umso dankbarer bin ich, dass Sabine und ich es wirklich durchgezogen haben und im Januar nach Australien geflogen sind. Und vor allem du und deine liebenswerte Art habt diesen Urlaub für uns unvergesslich gemacht, als du bei Eurosport das Handtuch für uns signiert hast! Jedes Mal, wenn ich mir das Video anschaue, bekomme ich Gänsehaut und mir treten Tränen der Freude in die Augen. Du gibst uns Fans so viel zurück und wir sind mega stolz, dass wir schon seit fast neun Jahren treue Fans von dir sind 😊!!! Dank der gemeinsamen Leidenschaft für dich als Spielerin und Mensch hab ich schon so viele tolle Menschen sowohl in Deutschland verteilt als auch aus dem Rest der Welt kennen lernen dürfen. Danke auch, dass du und Joschi in dieser Gruppe immer den Kontakt zu Jörgen und somit zu uns haltet. Das ist echt nicht selbstverständlich und wir wissen das sehr zu schätzen. </div>
                    <div>Mein Sohn Colin und ich wünschen dir nur das Beste für die Zukunft und ich hoffe, dich bald wieder fit auf dem Platz spielen zu sehen. Viel Erfolg auch bei dem digitalen Tennis-Turnier – ich hoffe, das wir Fans davon auch etwas zu sehen bekommen 😉. Bleib gesund und wie du bist</div>
                    <div>Diana aus München 😊
                    </div>
                  </div>
                </el-card>
              </el-col>
            </el-row>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont camera"></i>Foto</div>
            <div>
              <el-card style="margin-bottom: 40px">
                <el-image
                  style="width:100%"
                  src="https://ftp.bmp.ovh/imgs/2020/04/cee26015bce026a5.jpg"
                ></el-image>
              </el-card>
              <el-card style="margin-bottom: 40px">
                <el-image
                  style="width:100%"
                  src="https://ftp.bmp.ovh/imgs/2020/04/3861423755472126.jpg"
                ></el-image>
              </el-card>
              <el-card>
