          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont cook"></i>Zubereitung
            </div>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-1"></i>Zwiebel klein schneiden; in heißem Öl in großem Topf anbraten; kleine Prise Zucker dazu;
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-2"></i>Gehacktes dazu geben und durchbraten; Öl abschütten und dann mit Salz und Pfeffer würzen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-3"></i>Tomatenmark in einer kleinen Tasse Wasser auflösen und zu Gehacktem geben; gehackte und passierte Tomaten dazugeben und etwa 5 Minuten kochen lassen;
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-4"></i>nun würzen - immer die komplette Oberfläche vom Topf: Basilikum, Kräuter de Provence, Rosmarin, Oregano und Majoran und umrühren;
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-5"></i>dann etwas weniger von Liebstöckel, Paprika, Knoblauch und Cayenne-Pfeffer und wieder umrühren;
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-6"></i>dann nochmal von den ersten Gewürzen hinzugeben und abschmecken
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-7"></i>
                <div>Nun in einer großen Auflaufform Schichten:
                </div>
                <div>Sauce, bis der Boden komplett bedeckt ist
                </div>
                <div>6 Scheiben Käse
                </div>
                <div>3 Platten Lasagne-Platten
                </div>
                <div>Sauce
                </div>
                <div>Käse
                </div>
                <div>Nudeln
                </div>
                <div>Sauce
                </div>
                <div>Käse
                </div>
                <div>Nudeln
                </div>
                <div>Sauce (muss den Schluss bilden)
                </div>
                <div>Käse gibt das Ende vor ;)</div>
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-8"></i>Nun noch die Bechamelsauce: Butter erhitzen, Mehl für Mehlschwitze hinzugeben, dann mit Sahne auffüllen und unter Rühren aufkochen; abschmecken mit Salz, Pfeffer und Muskat und oben auf die Auflaufform geben.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-9"></i>Dann in den vorgeheizten Backofen bei 200 Grad etwa 30 Minuten backen - ein Traum, den bisher alle Gäste geliebt haben :)
              </div>
            </el-card>
          </span>
        </el-dialog>
        <!--R12-->
        <el-dialog :visible.sync="Dialog_R12">
          <span>
            <div class="heading_title">
              Gurken-Lachs-Pfanne
            </div>
          </span>
          <span>
            <el-row
              gutter="60"
              type="flex"
              justify="center"
            >
              <el-col span="4">
                <el-avatar
                  :size="120"
                  fit="fit"
                  src="https://i.postimg.cc/MGB3QTzL/image.png"
                ></el-avatar>
              </el-col>
              <el-col span="20">
                <div
                  class="heading_name"
                  style="margin-bottom:10px"
                >
                  Sandra Strube
                </div>
                <el-card
                  class="card_chat"
                  shadow="never"
                >
                  <div class="text_content">
                    <div>
                      Liebe Angie,</div>
                    <div>es macht immer Riesen Freude, dich auf dem Platz spielen zu sehen. Wie du alles gibst, um jeden Ball kämpfst und bis zum bitteren Ende versuchst, ein Match noch drehen zu können. Das macht dich als Sportlerin aus und das lieben wir - deine Fans - so an dir. Ich hoffe, wir werden dich noch lange auf dem Platz anfeuern können. Bleib so wie du bist :-) Aber vor allem: BLEIB GESUND! Denn das ist das Wichtigste ;-)</div>
                    <div>Liebe Grüße, Sandra
                    </div>
                  </div>
                </el-card>
              </el-col>
            </el-row>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont shucai"></i>Zutaten
            </div>
            <el-card
              class="card_tag"
              shadow="never"
            >350 g Lachsfilet(s) ohne Haut
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 große Salatgurke(n) (ca. 500 g)
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >150 ml Schlagsahne
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >250 ml Gemüsebrühe
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 kleine Zwiebel(n)
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >2 TL Mehl
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >2 TL Senf, körnigen
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Petersilie, glatte
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Salz und Pfeffer
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Zitronensaft</el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Öl</el-card>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont cook"></i>Zubereitung
            </div>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-1"></i> Die Gurke schälen, halbieren und mit einem Löffel die Kerne entfernen. Anschließend die Gurke in ca. 1,5 cm breite Halbringe schneiden.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-2"></i> Die Zwiebel fein würfeln und in einer Pfanne mit Öl (ca. 1 EL) anschwitzen. Die Gurke dazugeben und 2 Minuten mit anbraten. 2 TL Mehl darüber sträuben und kurz mitbraten. Gemüsebrühe, Schlagsahne und den körnigen Senf dazugeben und bei mittlerer Hitze ca. 8 Minuten schmoren lassen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-3"></i> In der Zeit den Lachs in ca. 2,5 x 2,5 cm große Würfel schneiden. Danach zu den Gurken geben und 7 - 8 Minuten weiterschmoren. Mit Salz, Pfeffer und Zitronensaft abschmecken. Die Petersilie fein hacken und kurz vor dem Servieren unterheben. Dazu schmeckt Reis oder Kartoffeln.
              </div>
            </el-card>
          </span>
        </el-dialog>
        <!--R13-->
        <el-dialog :visible.sync="Dialog_R13">
          <span>
            <div class="heading_title">
              Garnelenpfanne
            </div>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont cook"></i>Zubereitung
            </div>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-1"></i> Als erstes werden die Garnelen (8/12er easy Peel ) in Olivenöl qlasigangebraten. Danach in eine Schússel qeben und zur Seite stellen. Alternativ kann man natürlich auch Hähnchenfilet oder Steakhüfte nehmen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-2"></i> Dann werden 2 mittelgroBe Zwiebeln in Butter angedünstet. 3 mittelscharfe Pepperoni und 250 Gramm Champignons mit dazu qeben. Kurz mitbraten. In der Zwischenzeit die Nudeln kochen. Idealerweise Tagliatelle.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-3"></i>Das Gemúse mit 1.5 Esslöffel Mehl bestäuben und verrühren. Alles mit ca 600 mal Gemüsebrühe ablöschen. Kurz aufkochen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-4"></i>Nun 250 Gramm Philadelphia Frischkäse einrühren. Mit Zitronenpfeffer abschmecken. Jetzt kommen noch ca 400 Gramm TK Erbsen hinzu. 5 Minuten mitkochen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-5"></i>Als letztes dann die Garnelen und die Nudeln hinzufügen. Nochmals abschmecker. Fertig. Reicht für 4 bis 6 Portionen und kostet ca 3 Euro pro Person.
              </div>
            </el-card>
          </span>
        </el-dialog>
        <!--R14-->
