              class="card_tag"
              shadow="never"
            >240 g Risottoreis
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >80 g Parmesan, gerieben
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Meersalz und Pfeffer
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Zitronenschale</el-card>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont cook"></i>Zubereitung
            </div>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-1"></i> Für die Spargelmousse den Spargel säubern, in Stücke schneiden und die Spargelköpfe zur Seite legen.Die „Spargelabfälle“ für 2 Minuten in kochendes Wasser geben, das mit Salz, Zucker und Butter gewürzt ist.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-2"></i> Nach 2 Minuten die Spargelreste herausnehmen und die vorbereiteten Spargelstücke ca. 6 Minuten in der Flüssigkeit garen. Herausnehmen, abkühlen lassen und zu Spargelmousse pürieren. Zuletzt die Spargelköpfe kurz im Kochwasser angaren, herausnehmen, mit Eiswasser abschrecken und für die Dekoration zur Seite stellen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-3"></i> Für das Risotto die Brühe zum Kochen bringen. In einem zweiten Topf das Olivenöl mit Butter und Olivenöl erhitzen und die gewürfelte Zwiebel darin sanft glasig dünsten.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-4"></i> Den Reis hinzufügen, die Temperatur höher schalten und die Mischung solange unter Rühren weiterdünsten, bis es im Topf „knistert“. Dabei darauf achten, dass die Masse keine Bräune annimmt. Mit dem Wermut/Wein ablöschen und weiterrühren bis die Flüssigkeit aufgesogen wurde.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-5"></i> Nun tassenweise die heiße Brühe dazugeben und unter fortwährendem Rühren das Risotto garen. Hierbei darauf achten, dass jedes Mal die Brühe vom Risotto komplett aufgesogen wurde, bevor neue Flüssigkeit hinzugefügt wird. Nach ca. 15 Minuten kurz probieren.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-6"></i> Die Reiskörner sollen weich sein, und dennoch „Biss“ haben. Das Risotto vom Herd ziehen. Zuletzt die Spargelmousse, die Zitronenschale und den Parmesan unterrühren, mit Salz und Pfeffer abschmecken und mit den Spargelspitzen dekorieren.
              </div>
            </el-card>
          </span>
        </el-dialog>
