                    </div>
                    <div>DU eine großartige Persönlichkeit auf UND neben dem Platz . Ich FREUE mich immer auf DEINE Spiele . DEINE Emotionen UND DEIN Wille in jedem Spiel DEIN BESTES zu geben machen DICH liebe ANGIE für UNS alle zu etwas ganz besonderen . Ganz liebe Grüße UND bleib einfach so wie DU bist.
                    </div>
                    <div>Joe
                    </div>
