              </el-card>
            </div>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont cook"></i>Zubereitung
            </div>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-1"></i> Milch leicht erwärmen . Hefe in einer Schüssel zerbröseln , mit 3- 4 EL der warmen ( nicht heißen ). Milch und den Zucker verrühren .
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-2"></i> 1Ei , restliche Milch , Salz , Mehl zufügen , Butter in kleinen Würfeln nach und nach mit dem Knethacken des Rührgeräts unterarbeiten , dann alles mit den Händen zu einem glatten Teig verkneten und diesen mit einem Tuch abgedeckt an einem warmen Ort ca 1 Std ruhen lassen
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-3"></i> Teig nochmals mit den Händen durchkneten , dann in. 3 Stücke teilen , auf bemehlter Fläche 3 Stränge von ca 40 cm lange rollen und auf einem mit Backpapier ausgelegten Blech zu einem lockeren Zopf flechten , diesen mit einem Tuch bedeckt weitere 30 Min ruhen lassen .
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-4"></i> Dann mit Eigelb bestreichen , Hagelzucker daraufstreuen , im 180 Grad heißen Ofen auf mittlerer. Schiene in ca 25 Min goldgelb backen und vor dem. Servieren vollständig auskühlen lassen .
              </div>
            </el-card>
          </span>
        </el-dialog>
