            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-1"></i> Brötchen in Wasser einweichen. Die Zwiebel schälen und in feine Würfel schneiden. Wer möchte, kann die Zwiebel auch kurz in Butter glasig dünsten (ich bevorzuge die rohen Zwiebeln).
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-2"></i> Das Ei, die Zwiebeln und die Gewürze zur Hackmasse geben und sehr gut vermengen, entweder mit einem großen Löffel oder mit den Händen. Nicht mit dem Mixer arbeiten, dabei werden die Frikadellen oft zäh!
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-3"></i> Wer die rohe Masse abschmecken kann, sollte das jetzt tun, oder eine Probe braten.Jetzt gleichmäßige, nicht zu kleine Bällchen/Klöße/Klopse formen und auf einer bemehlten Arbeitsfläche flachdrücken und glätten. Wer möchte, kann sie in Mehl oder Semmelbrösel wenden. Oma und ich braten sie "ohne alles".
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-4"></i> Eine schwere Pfanne mit guter Margarine stark erhitzen und die Frikadellen einlegen, kurz auf beiden Seiten scharf anbraten und dann ca. 15 - 20 Minuten (1 - 2-mal vorsichtig wenden) auf mittlerer/schwacher Hitze fertig braten. Nicht zu viele Frikadellen auf einmal in einer Pfanne braten, eher eine zweite benutzen oder nacheinander braten. Dazu passt z. B. Kartoffelsalat.
              </div>
            </el-card>
          </span>
        </el-dialog>
