            >
              <div>
                <i class="iconfont icon-4"></i>Etwas Öl in eine Pfanne geben und aus dem Pfannkuchenteig kleine Taler braten.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-5"></i>Das dritte Ei in einer kleinen Schale aufschlagen, sodass das Eigelb als Ganzes erhalten bleibt. Einen Topf bis zur Hälfte mit Wasser füllen, Essig dazugeben und das Wasser zum Sieden bringen. Die Temperatur des Herds anschließend senken und mit einem Kochlöffel das Wasser kräftig rühren, sodass eine Art „Whirlpool“ entsteht. Das rohe Ei vorsichtig in die Mitte des Strudels legen und 3-4 Min. im Wasser lassen. Das Eiklar legt sich dank des Strudels um das Eigelb und verfestigt sich dort.
