                    <div>Was mich anbetrifft, fahre auch gern in die Berge. Etwas außer- und oberhalb von Garmisch-Partenkirchen liegt das Riessersee Hotel mitten im Wald, die Luft ist da sehr rein und nach einem leckeren Essen lädt der kleine Bergsee zu einem gesunden Verdauungsspaziergang mit herrlichem Bergpanorama ein- unbedingt mal ausprobieren!
