              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-1"></i> Die Stielenden und Blattansätze vom Rhabarber entfernen. Die Stangen waschen und in kleine Stücke schneiden.
              </div>
            </el-card>
            <el-card
              shadow="never"
