                <i class="iconfont icon-3"></i> Inzwischen die EiweiB steif schlagen und nach und nach unter weiterem Rühren den Zitronensaft einträufeln und den übrigen Rohrzucker einrieseln. Die Springform aus dem Ofen nehmen, die Masse auf dem Rhabarber verteilen und mit Backpapier abgedeckt weitere 30 Minuten bei 150℃ backen.
