                    <div>hier ist ein Rezept aus einem Kochbuch für Singles. Das ist sehr praktisch, weil man dann keine Reste hat, die man im Kühlschrank vergisst und nach ein paar Tagen wegschmeißen muss. Wenn du für mehrere Personen kochen willst - vielleicht will die Petko oder so ja mal vorbeikommen - einfach entsprechend größere Mengen nehmen.
                    </div>
                    <div>Wenn man nicht meine favorisierte Alternative mit Schinken nimmt ist es vegetarisch. Muskatnuss ist nicht so meins, stattdessen nehme ich lieber Kräutersalz. Einfach ausprobieren was dir besser schmeckt.
                    </div>
                    <div>Das Foto stammt übrigens von einem Gossip Konzert, bei dem ich in der ersten Reihe stand. Das war eine Aktion vom Radiosender 1Live. Ich hatte die Karten, die man nicht kaufen konnte, gewonnen, wir hatten uns nach vorne gekämpft und dann war Beth Ditto erkältet und nach 20 Minuten nur Talk wieder weg. Schade, aber trotzdem ein schönes Erlebnis.
                    </div>
                    <div>Ich hoffe auch dich irgendwann mal live und in Farbe und vor allem gesund und munter sehen zu dürfen.
                    </div>
                    <div>Viel Spaß mit diesem und den anderen Rezepten. Enjoy.
                    </div>
                    <div>Schöne Grüße von Sualk Bolzano aus NRW
