            <el-card
              class="card_tag"
              shadow="never"
            >a 150 g
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >2 EL Erdnussöl
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >150 ml WeiBwein, trocken
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >300 ml kraftige Hühnerbri he oder LliD-Würzer-Brühe
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >100 g Walnüsse
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >3 Knoblauchzehen
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >150 g Naturjoghurt 1,5%
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 TL fettarme Butter
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 TL Paprikapulver, edelsüis
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Salz</el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Pfeffer aus der Mühle
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 TL. Zitronenabrieb
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >3 EL gehackte Petersilie
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >250 g Naturreis-Mischung
            </el-card>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont cook"></i>Zubereitung
            </div>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-1"></i> Die Kartoffeln waschen, schälen und in 2 cm breite Scheiben schneiden. Die Brühe zum Kochen bringen, die Kartoffelscheiben hineinlegen, bei milder Temperatur in etwa 15 Minuten micht zu weich garen und abtropfen lassen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-2"></i> Das Fleisch waschen und trocken tupfen, die Zwiebeln grob hacken, die Chilischote putzen, entkernen und fein schneiden, die Rote Bete waschen und in kleine Würfel schneiden.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-3"></i> Das öl erhitzen und die Fleischwürfel darin anbräunen, die Zwiebeln und die gehackte Chilischote zugeben, kräftig durchbraten, mit Salz, Pfeffer, Paprikapulver und Curry pikant würzen
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-4"></i> Den Ajvar einrühren, das Sauerkraut und die Rote Bete zufúgen, mit der Brühe ablöschen und etwa 15 Minuten bei milder Hitze kochen, dabei öfter umrühren.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-5"></i> Zum Schluss die Petersilie und die Sahne zugeben und nochmais abschmecken.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-6"></i> Die Kartoffelscheiben auf die Teller legen, das Putengulasch dazu anrichten und mit etwas Petersilie garnieren.
              </div>
            </el-card>
          </span>
        </el-dialog>
        <!--R33-->
        <el-dialog :visible.sync="Dialog_R33">
          <span>
            <div class="heading_title">
              Schoko Kirsch Kuchen
            </div>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont shucai"></i>Zutaten
            </div>
            <el-card
              class="card_tag"
              shadow="never"
            >200gr weiche Butter
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >175+50gr Zucker
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >5 Eigelb
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >5 Eiweiß
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >100gr gemahlene Haselnüsse
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >200gr geriebene Schokolade
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 EL Zimt
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >180gr Mehl
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 Päckchen Backpulver
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >350gr entsteinte Sauerkirschen
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Puderzucker zum Besieben
            </el-card>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont distance"></i>Zeit
            </div>
            <div>
              <el-card
                class="card_time"
                shadow="never"
              >40-60 Minuten bei 180Grad Ober Unterhitze. Springform 26cm</el-card>
            </div>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont cook"></i>Zubereitung
            </div>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-1"></i> Den Ofen vorheizen. Für den Rührteig die Butter geschmeidig rühren, nach und nach 175gr Zucker und die Eigelbe eins nach dem anderen zugeben und schaumig rühren. Die Nüsse, Schokolade und den Esslöffel Zimt unterrühren. Die Eiweiße mit 50gr Zucker steif schlagen und auf den Teig gleiten lassen. Das Mehl mit dem Backpulver darüber sieben und alles vorsichtig vermischen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-2"></i> Zwei Drittel des Teiges in die gefette Backform füllen. Die Kirschen darauf verteilen. Den restlichen Teig einfüllen und glatt streichen. Den Kuchen backen
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-3"></i> Nach dem Abdämpfen den Formrand lösen, Abnehmen und den Kuchen auf einem Kuchengitter auskühlen lassen. Den abgekühlten mit etwas Puderzucker besieben und servieren
              </div>
            </el-card>
          </span>
        </el-dialog>
        <!--R34-->
        <el-dialog :visible.sync="Dialog_R34">
          <span>
            <div class="heading_title">
              Haselnusskranz
            </div>
          </span>
          <span>
            <el-row
              gutter="60"
              type="flex"
              justify="center"
            >
              <el-col span="4">
                <el-avatar
                  :size="120"
                  fit="fit"
                  src="https://i.postimg.cc/5yQR55cw/image.png"
                ></el-avatar>
              </el-col>
              <el-col span="20">
                <div
                  class="heading_name"
                  style="margin-bottom:10px"
                >
                  Sandra Schoen
                </div>
                <el-card
                  class="card_chat"
                  shadow="never"
                >
                  <div class="text_content">
                    <div>
                    </div>
                    <div>Liebe Angie
                    </div>
                    <div>Diese beiden Rezepte sind sehr einfach und schnell zubereitet . Außerdem sehr , sehr lecker . Vielleicht hast du ja mal die Zeit dafür , sie zu backen 🤗.
                    </div>
                    <div>Bleib uns bitte auf dem Tennisplatz noch lange erhalten und vor allem gesund.
                    </div>
                    <div>Ganz liebe Grüße , deine Sandra
                    </div>
                  </div>
                </el-card>
              </el-col>
            </el-row>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont camera"></i>Foto</div>
            <div>
              <el-card>
                <el-image
                  style="width:100%"
                  src="https://ftp.bmp.ovh/imgs/2020/04/5080207082518e92.jpeg"
                ></el-image>
              </el-card>
            </div>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont shucai"></i>Zutaten
            </div>
            <el-card
              class="card_tag"
              shadow="never"
            >250 gr Butter
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >250 gr Zucker
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >250 gr gemahlene Haselnüsse
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >400 gr Mehl
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >4 Eier
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 pck Backpulver
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >2 pck Vanillezucker
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >250 ml Milch
            </el-card>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont cook"></i>Zubereitung
            </div>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-1"></i> Alles in einer Schüssel verrühren In eine eingefettete Form geben
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-2"></i> Bei 200 Grad ca. 50 - 60 min
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-3"></i> backen ( Stabchentest )
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-4"></i> Ruckzuck gemacht und superlecker
              </div>
            </el-card>
          </span>
        </el-dialog>
        <!--R35-->
        <el-dialog :visible.sync="Dialog_R35">
          <span>
            <div class="heading_title">
              Zitronenhuhn aus dem Ofen
            </div>
          </span>
          <span>
            <el-row
              gutter="60"
              type="flex"
              justify="center"
            >
              <el-col span="4">
                <el-avatar
                  :size="120"
                  fit="fit"
                >
                  <div class="avatar_text">CB</div>
                </el-avatar>
              </el-col>
              <el-col span="20">
                <div
                  class="heading_name"
                  style="margin-bottom:10px"
                >
                  Claudia Bredthauer
                </div>
                <el-card
                  class="card_chat"
                  shadow="never"
                >
                  <div class="text_content">
                    <div>
                      Liebe Angie,
                    </div>
                    <div>als Boris Becker Wimbledon gewann war ich 13 und über Nacht der größte Tennisfan überhaupt. Ich wollte damals sogar meine blonden Haare rot färben 🤣.
                    </div>
                    <div>Ich erinnere mich an tolle Davis Cup Schlachten und wie ich nachts heimlich ins Wohnzimmer geschlichen bin, um die Turniere in den Staaten zu schauen und dann tagsüber müde in der Schule saß.
                    </div>
                    <div>Als die Ära Becker/Graf vorbei war, hatte ich irgendwann das Interesse verloren und ich habe jahrelang kein Tennis mehr geschaut.
                    </div>
                    <div>Ich glaube es war 2015 - da bin ich beim zappen im TV bei Eurosport hängengeblieben. Im TV liefen die US Open und gezeigt wurde das Match Kerber vs. Azarenka. Da war soviel Spannung und geniale Ballwechseln dabei. Und obwohl du das Match nicht gewonnen hast, war ich von deinem Kampfgeist begeistert - wie du um jeden Punkt gefightet hast.
                    </div>
                    <div>Auch wenn man verliert, kann man gewinnen...dass war mein Gedanke damals.
                    </div>
                    <div>Das Match hat mir die Freude an den Sport zurückgebracht und ich durfte seitdem viele tolle Spiele sehen - von dir und auch von vielen anderen tollen Spieler/innen.
                    </div>
                    <div>Danke, dass du mir die Freude an diesen tollen Sport zurückgebracht hast.
                    </div>
                    <div>Dir, deiner Familie und allen Leuten in deinem Umfeld alles Gute und viel Gesundheit.
                    </div>
                    <div>Liebe Grüße
                    </div>
                    <div>Claudia
                    </div>
                  </div>
                </el-card>
              </el-col>
            </el-row>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont camera"></i>Foto</div>
            <div>
              <el-card>
                <el-image
                  style="width:100%"
                  src="https://ftp.bmp.ovh/imgs/2020/04/77d4d7301ef3267f.jpg"
                ></el-image>
              </el-card>
            </div>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont shucai"></i>Zutaten
            </div>
            <el-card
              class="card_tag"
              shadow="never"
            >6 Hähnchenteile/Schenkel
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >4 unbehandelte kleine Zitronen
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >120 ml Olivenöl
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >3 TL Salz
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 TL Pfeffer
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >2 TL Paprikapulver
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 bis 2 Stängel Rosmarin, Thymian
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 Knolle Knoblauch
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >6 kleine rote o weiße Zwiebeln
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 kg kleine Kartoffeln
            </el-card>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont cook"></i>Zubereitung
            </div>
            <div class="heading_subtitle">Zubereitung Marinade
            </div>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-1"></i> Die Zitronen rollen und den Saft auspressen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-2"></i> 1 Zitrone in dünne Scheiben schneiden.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-3"></i> Zitronensaft mit Olivenöl, Salz, Pfeffer und Paprika verrühren.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-4"></i> Die Hähnchenteile mit der Marinade bestreichen und in einer luftdichten Schüssel - am besten über Nacht - alternativ mind. 4 Stunden marinieren.
              </div>
            </el-card>
            <div class="heading_subtitle">Zubereitung Ofen
            </div>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-1"></i> Halbieren die Kartoffeln und viertele die Zwiebeln. Lege beides in eine ofenfeste Form. Halbiere die Knoblauchknolle und lege sie mit der Schnittfläche nach unten in die Backform.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-2"></i> Lege die Kräuter mit dazu und die Hähnchenteile dazwischen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-3"></i> Übergieße alles mit der übrigen Marinade.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-4"></i> Lege die Zitronenscheiben über die Hähnchenteile und backe das Gericht bei 200 Grad bei Ober/Unterhitze ca. 40 bis 60 min.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-5"></i> Übergieße die Hähnchenteile hin u wieder mit dem Sud und schiebe das Backblech unter den Grill auf höchster Stufe, bis das Hähnchen Farbe bekommt.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-6"></i> Ein leckeres und einfaches "One Pot Gericht" Hauptspeise und Beilage in einer Form. Guten Appetit.
              </div>
            </el-card>
          </span>
        </el-dialog>
        <!--R36-->
        <el-dialog :visible.sync="Dialog_R36">
          <span>
            <div class="heading_title">
              Mousse au Chocolat
            </div>
          </span>
          <span>
            <el-row
              gutter="60"
              type="flex"
              justify="center"
            >
              <el-col span="4">
                <el-avatar
                  :size="120"
                  fit="fit"
                >
                  <div class="avatar_text">DG</div>
                </el-avatar>
              </el-col>
              <el-col span="20">
                <div
                  class="heading_name"
                  style="margin-bottom:10px"
                >
                  Dennis Giese
                </div>
                <el-card
                  class="card_chat"
                  shadow="never"
                >
                  <div class="text_content">
                    <div>
                      Liebe Angie
                    </div>
                    <div>Selbst als Spitzensportler sollte man es sich nicht nehmen lassen, auch mal dem Schleckermaul in sich nachzugeben.
                    </div>
                    <div>Und daher möchte ich dir gerne mein Rezept für eine leckere Mousse au Chocolat geben. Ich bin selbst gelernter Koch und liebe die Patisserie und alles mit Schokolade. Und in meiner Ausbildung war es das erste richtige Rezept, das ich damals umsetzen durfte.
                    </div>
                    <div>Also los geht’s ...
                    </div>
                    <div>Liebe Angie! Ich wünsche Dir ganz viel Spaß beim Nachkochen und Genießen!!!!
                    </div>
                    <div>Bleib so wie Du bist und genieß Dein Leben mit und irgendwann mal ohne Tennis. Alles Glück der Welt für Dich!!!!
                    </div>
                    <div>Grüße aus der schönen Pfalz
                    </div>
                    <div>Dein Fan Dennis
                    </div>
                  </div>
                </el-card>
              </el-col>
            </el-row>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont shucai"></i>Zutaten
            </div>
            <div class="heading_subtitle">Für 4 Personen</div>
            <el-card
              class="card_tag"
              shadow="never"
            >6 Eigelb
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >200g Zucker
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >5cl Amaretto-Likör
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >500g Zartbitter Schokolade
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >500ml Sahne
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >2 Päckchen Sahnesteif
            </el-card>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont cook"></i>Zubereitung
            </div>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-1"></i> Die Schokolade klein hacken und langsam im Wasserbad schmelzen. Beiseite stellen und etwas abkühlen lassen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-2"></i> Eier trennen. Das Eiweiß wird nicht benötigt. Das Eigelb zusammen mit dem Zucker und dem Amaretto in eine große Rührschüssel geben und kurz mit einem Schneebesen verrühren.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-3"></i> Einen Kochtopf (auf den man die Rührschüssel aufsetzen kann, muss also etwas größer als die Schüssel sein) zu einem Viertel mit Wasser füllen und dieses zum Kochen bringen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-4"></i> Sobald das Wasser kocht, die Rührschüssel auf den Topf aufsetzen und die Eigelbmasse unter ständigem Rühren mit dem Schneebesen schaumig schlagen. Hierbei aufpassen, dass man immer schlägt und die Masse auch vom Rand löst, da sonst Rührei entstehen kann, was hier nicht gewünscht ist. Es muss ein Schaum entstehen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-5"></i> Ob die Masse schaumig genug ist, kann man mit der Rückseite eines Esslöffels testen. Diese leicht in die Masse eintauchen und dann dagegen pusten. Wenn der Schaum leichte Wellen schlägt und es aussieht wie Rosenblüten, dann ist die Masse richtig aufgeschlagen. Daher nennt man das auch “zur Rose abziehen”!
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-6"></i> Die Masse vom Kochtopf nehmen und zur Seite stellen. Ins Spülbecken kaltes Wasser einlassen und die Schüssel mit der Eigelbmasse dort hineinstellen, gelegentlich umrühren. Auf Handtemperatur abkühlen. Währenddessen die Sahne mit dem Sahnesteif steifschlagen und nochmals kurz kaltstellen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-7"></i> Die Schüssel aus dem kalten Wasserbad nehmen und vorsichtig nach und nach die abgekühlte aber noch flüssige Schokolade unterrühren. Es entsteht eine zähe Schokoladenmasse.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-8"></i> Zuletzt die Sahne nach und nach unterheben mit dem Schneebesen. Die fertige Masse in eine große Glasschüssel oder kleine Dessertgläser abfüllen und vor dem Servieren mindestens 4-5 Stunden kaltstellen. Dazu kann man prima frische Früchte oder Vanillesoße reichen.
              </div>
            </el-card>
          </span>
        </el-dialog>
        <!--R37-->
