              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-2"></i> Krauter, Schmand, saure Sahne, Joghurt und Mayonnaise mit dem Schneidstab fein pürieren.
              </div>
            </el-card>
            <el-card
              shadow="never"
