              <div>
                <i class="iconfont icon-3"></i> So lange pürieren, bis die Sauce grün ist und die Kräuter fein zerkleinert sind. Mit Salz, Pfeffer und evtl. Zitronensaft oder Essig abschmecken.
              </div>
            </el-card>
            <el-card
              shadow="never"
