        <!--R38-->
        <el-dialog :visible.sync="Dialog_R38">
          <span>
            <div class="heading_title">
              Badische Lauchsuppe
            </div>
          </span>
          <span>
            <el-row
              gutter="60"
              type="flex"
              justify="center"
            >
              <el-col span="4">
                <el-avatar
                  :size="120"
                  fit="fit"
                >
                  <div class="avatar_text">TM</div>
                </el-avatar>
              </el-col>
              <el-col span="20">
                <div
                  class="heading_name"
                  style="margin-bottom:10px"
                >
                  Thomas Moll
                </div>
                <el-card
                  class="card_chat"
                  shadow="never"
                >
                  <div class="text_content">
                    <div>
                      Hallo Angie Kerber,
                    </div>
                    <div>herzliche Grüße, sende ich Dir mit einem Rezept aus meiner badischen Heimat, dass leicht nachzukochen ist und ausgesprochen lecker schmeckt.
                    </div>
                  </div>
                </el-card>
              </el-col>
            </el-row>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont shucai"></i>Zutaten
            </div>
            <el-card
              class="card_tag"
              shadow="never"
            >2 Stk. Zwiebeln
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >125 g roher Schinken
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >40 g Butter
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 Liter heiße Brühe
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >4 Stangen Lauch
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Salz und Pfeffer
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 EL Mehl
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >0,125 Liter Weißwein
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >2 EL Sauerrahm
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >2 Stk. Eigelb
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >25 g Emmentaler gerieben
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >0,5 Bund Petersilie
            </el-card>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont cook"></i>Zubereitung
            </div>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-1"></i> Zwiebelwürfel 3-4 min in Butter Glasig dünsten, Schinkenwürfel zugeben und 3 min mitbraten lassen, mit wenig Brühe ablöschen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-2"></i> Lauch in 1 cm Stücke schneiden, im entstandenen Fond 5 min garen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-3"></i> Die restliche Brühe zugeben, mit Salz & Pfeffer abschmecken und nochmals 15 min ziehen lassen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-4"></i> Das Mehl mit Weißwein glatt rühren und unter rühren in die Suppe geben. Dann wird der Sauerrahm zugegeben. Das Eigelb mit etwas Suppe verquirllen und unterrühren.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-5"></i> Die Lauchsuppe in feuerfeste Gefäße gießen, mit Käse bestreuen und im Grill oder Backofen überkrusten.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-6"></i> Petersilie fein hacken und vor dem Servieren darüber streuen.
              </div>
            </el-card>
          </span>
        </el-dialog>
        <!--R39-->
        <el-dialog :visible.sync="Dialog_R39">
          <span>
            <div class="heading_title">
              Zucchiniplätzchen
            </div>
          </span>
          <span>
            <el-row
              gutter="60"
              type="flex"
              justify="center"
            >
              <el-col span="4">
                <el-avatar
                  :size="120"
                  fit="fit"
                >
                  <div class="avatar_text">EF</div>
                </el-avatar>
              </el-col>
              <el-col span="20">
                <div
                  class="heading_name"
                  style="margin-bottom:10px"
                >
                  Elisabeth Feldes
                </div>
                <el-card
                  class="card_chat"
                  shadow="never"
                >
                  <div class="text_content">
                    <div>
                      Dieses Gericht eignet sich als Beilage zu Fleischgerichten oder auch zu Fisch. Es schmeckt aber auch als vegetarisches Gericht mit einem Kräuterdip dazu, außerdem ist es ein kohlenhydratarmes Gericht, da man auf Kartoffeln verzichtet. Ein frischer Salat rundet die Zucchiniplätzchen ab.
                    </div>
                    <div>Guten Appetit wünscht Elisabeth Feldes
                    </div>
                  </div>
                </el-card>
              </el-col>
            </el-row>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont shucai"></i>Zutaten
            </div>
            <el-card
              class="card_tag"
              shadow="never"
            >500 bis 600 gr. Zucchini mit Schale und gewaschen
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >etwas Thymian
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 kleine Knoblauchzehe
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 Ei
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >2-3 EL Speisestärke
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Muskatnuss, Pfeffer und Salz
            </el-card>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont cook"></i>Zubereitung
            </div>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-1"></i> Zucchini waschen und grob in einer Reibe reiben, danach über einem Sieb abtropfen lassen damit sie Wasser verliert.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-2"></i> Knoblauch pressen und mit dem Ei, der Speisestärke, Thymian, Salz und Pfeffer vermischen, danach die Zucchini dazu geben und gut vermischen, je nach Konsistenz noch etwas Speisestärke dazu geben.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-3"></i> Öl in einer Pfanne erhitzen und dünne, goldbraune Plätzchen ausbacken, ca. 1-1,5 EL Teig in die Pfanne geben und etwas platt drücken. Immer mal wieder wenden damit sie nicht braun werden.
              </div>
            </el-card>
          </span>
        </el-dialog>
        <!--R40-->
        <el-dialog :visible.sync="Dialog_R40">
          <span>
            <div class="heading_title">
              EULA MAE'S CRAB AND SHRIMP SAUCE PIQUANTE
            </div>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont shucai"></i>Zutaten
            </div>
            <el-card
              class="card_tag"
              shadow="never"
            >1 cup peanut oil
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >3/4 cup all-purpose flour
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >3 1/2 cups chopped yellow onions
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 cup chopped celery
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >6 garlic cloves, peeled
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 cup seeded and chopped green bell peppers
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 cup seeded and chopped red bell peppers
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >One 6-ounce can tomato paste
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >One 8-ounce can tomato sauce
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >One 10-ounce can diced tomatoes with green chiles
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Salt and freshly ground black pepper to taste
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >3/4 teaspoon Tabasco brand pepper sauce
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >4 bay leaves
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >2 pounds medium-size shrimp, peeled and deveined
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >2 pounds jumbo lump crabmeat, picked over for shells and cartilage
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 cup water, or more as needed
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >2 tablespoons chopped fresh flat-leaf parsley leaves
            </el-card>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont cook"></i>Zubereitung
            </div>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-1"></i> Heat the oil in a large, heavy pot or Dutch oven over medium heat for 2 minutes. Add the flour and, stirring slowly and constantly, make a roux the color of peanut butter (see page 94). It will take 20 to 30 minutes
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-2"></i> Add the onions, celery, and garlic and cook, stirring, until soft, about 8 minutes. Add the bell peppers and cook, stirring, for 3 minutes. Add the tomato paste, tomato sauce, and tomatoes with chiles and stir to mix well. Season with salt, black pepper, and 1/4 teaspoon of the Tabasco. Add the bay leaves. Reduce the heat to medium-low, cover the pot, and let simmer for 1 hour
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-3"></i> Sprinkle 1/4 teaspoon of the Tabasco on the shrimp and the remaining 1/4 teaspoon Tabasco on the crabmeat. Add the shrimp to the tomato sauce and cook, stirring gently once or twice, until they turn pink, about 6 minutes.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-4"></i> Add the water and cook, stirring occasionally, for 5 minutes. (The sauce should be thick and smooth. If it appears to be too dry, add a little more water.) Gently fold in the crabmeat. Do not stir; gently shake the pot. Cover and cook for 10 minutes. Remove the bay leaves, add the parsley, and serve hot over the rice.
              </div>
            </el-card>
          </span>
        </el-dialog>
        <!--R41-->
