                <i class="iconfont icon-3"></i> Eine Auflaufform mit etwas Butter einfetten. Nudeln, Buttergemüse und Kochschinken in die Auflaufform geben, mit Sahne übergießen, alles salzen und pfeffern und mit geriebenem Emmentaler bestreuen. Im vorgeheizten Backofen bei 180° auf der mittleren Schiene überbacken, bis der Käse goldbraun ist.
              </div>
            </el-card>
          </span>
        </el-dialog>
