            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-1"></i> Die Pinienkerne in einer kleinen Pfanne,ohne Öl, trocken anrösten. Vorsicht, sie brennen leicht an!
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-2"></i> Den Käse möglichst frisch reiben und alle anderen Zutaten grob zerschneiden und anschließend alles zusammen mit dem Olivenöl pürieren.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-3"></i> Das kann man in einem Blender oder mit einem Pürierstab (zB. ESGE-Zauberstab) machen. Es sollte eine dicke, cremige Sauce werden, die sehr aromatisch duftet.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-4"></i> Anschließend die Spaghetti, nach Anweisung auf dem Paket, abkochen, schön al dente, dann abgießen und mit einigen Löffeln Pesto vermischen. Der Geschmack des frischen Pesto lässt sich nicht vergleichen mit dem Pesto aus dem Glas - es ist viiiiiel besser! 😋
              </div>
            </el-card>
          </span>
        </el-dialog>
        <!--R43-->
        <el-dialog :visible.sync="Dialog_R43">
          <span>
            <div class="heading_title">
              Nudelsalat von Mutti
            </div>
          </span>
          <span>
            <el-row
              gutter="60"
              type="flex"
              justify="center"
            >
              <el-col span="4">
                <el-avatar
                  :size="120"
                  fit="fit"
                >
                  <div class="avatar_text">MW</div>
                </el-avatar>
              </el-col>
              <el-col span="20">
                <div
                  class="heading_name"
                  style="margin-bottom:10px"
                >
                  Mario Westphal
                </div>
                <el-card
                  class="card_chat"
                  shadow="never"
                >
                  <div class="text_content">
                    <div>
                      Hallo Angie,
                    </div>
                    <div>ich bin ein großer Fan von dir und dank dir habe ich mit dem Tennis begonnen, zwar war ich schon 20 Jahre alt, aber ich bin dir sehr dankbar, dass du das Interesse in mir für diesen tollen Sport geweckt hast.
                    </div>
                    <div>Ich werde zwar nicht mehr der größte Tennisspieler, aber ich bin schon zufrieden, wenn der Ball im gegnerischen Spielfeld landet. 😃
                    </div>
                    <div>Da ich kein großer Koch bin, aber ein Fan von guten Salaten, möchte ich dir das Rezept für den besten Nudelsalat nicht vorenthalten.
                    </div>
                    <div>Für mich darf dieser Nudelsalat beim Grillen nicht fehlen und ich hoffe, dass er dir auch so schmeckt wie mir.
                    </div>
                    <div>Mit lieben Grüßen
                    </div>
                    <div>Mario
                    </div>
                  </div>
                </el-card>
              </el-col>
            </el-row>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont shucai"></i>Zutaten
            </div>
            <el-card
              class="card_tag"
              shadow="never"
            >1 Glasgurkentaler
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 große rote Paprika
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 kleine Dose Mais
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 Packung Kochschinken
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >ca. 500g Magerjoghurt
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 Glas Miracel Whip Classic
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Petersilie oder Schnittlauch (tiefgefroren)
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >500 g Nudeln (Schleifen oder Spirellis)
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >2 Zwiebeln
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Salz</el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Pfeffer</el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Paprikapulver
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >etwas Zucker
            </el-card>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont cook"></i>Zubereitung
            </div>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-1"></i> Die Nudeln 2 min weniger kochen als die Anleitung vorgibt.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-2"></i> Die Flüssigkeit der Gurkentaler in eine Schüssel geben und die Paprika klein schneiden und dazu geben. Ebenfalls den Mais in die Schüssel geben.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-3"></i> Wenn die Nudeln fertig gekocht sind, diese warm dazugeben. Anschließend mit Salz, Pfeffer, Paprikapulver und etwas Zucker würzen. Die Nudeln müssen im Sud öfter umgerührt werden. Dann den Salat erkalten lassen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-4"></i> Nun den Kochschinken in Würfeln, den Joghurt und die Mayonnaise hinzugeben. Alles miteinander vermischen und anschließend die Petersilie oder den Schnittlauch dazu geben.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-5"></i> Dann den Nudelsalat etwas durchziehen lassen und nochmal abschmecken.
              </div>
            </el-card>
          </span>
        </el-dialog>
        <!--R44-->
        <el-dialog :visible.sync="Dialog_R44">
          <span>
            <div class="heading_title">
              Nussecken
            </div>
          </span>
          <span>
            <el-row
              gutter="60"
              type="flex"
              justify="center"
            >
              <el-col span="4">
                <el-avatar
                  :size="120"
                  fit="fit"
                >
                  <div class="avatar_text">SE</div>
                </el-avatar>
              </el-col>
              <el-col span="20">
                <div
                  class="heading_name"
                  style="margin-bottom:10px"
                >
                  Sebastian
                </div>
                <el-card
                  class="card_chat"
                  shadow="never"
                >
                  <div class="text_content">
                    <div>
                      Hallo Angie,
                    </div>
                    <div>ich hatte mich schon so darauf gefreut dich in Bad Homburg live Tennis spielen zu sehen. Leider wurde das Turnier ja auch abgesagt. Aber aufgeschoben ist ja bekanntlich nicht aufgehoben :-)
                    </div>
                    <div>Zur Überbrückung bis zu deinem nächsten Match habe ich dir mein Lieblingsrezept vorbereitet.
                    </div>
                    <div>Als Single in der Coronazeit habe ich mich im Backen ausprobiert und ich muss sagen, dass es mir richtig Spaß macht.
                    </div>
                    <div>Viele Grüße aus Münnerstadt aus dem schönen Frankenland in Bayern,
                    </div>
                    <div>Sebastian
                    </div>
                  </div>
                </el-card>
              </el-col>
            </el-row>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont shucai"></i>Zutaten
            </div>
            <div class="heading_subtitle">FÜR DEN TEIG
            </div>
            <el-card
              class="card_tag"
              shadow="never"
            >25 g Butter
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >100 g Zucker
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >2 Eier
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >300 g Mehl
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 TL Backpulver
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 Pck. Vanillezucker
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >etwas Butter für die Form
            </el-card>
            <div class="heading_subtitle">FÜR DEN BELAG
            </div>
            <el-card
              class="card_tag"
              shadow="never"
            >200 g Aprikosenmarmelade
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >175 g Butter
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >130 g Zucker
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 Pck. Vanillezucker
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >400 g gemahlene Nüsse
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >200 g Zartbitterkuvertüre
            </el-card>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont cook"></i>Zubereitung
            </div>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-1"></i> Backofen auf 180 Grad (Umluft: 160) vorheizen. Ein Backblech (39x26x4 cm) gut einfetten.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-2"></i> Für den Teig Butter, Zucker, Vanillezucker und Eier miteinander verkneten. Mehl, Backpulver miteinander vermischen und mit dem restlichen Teig verkneten. Teig in das Backblech geben und zurecht drücken, damit ein ebener Boden entsteht (Alternativ mit dem Nudelholz ausrollen und dann reinlegen).
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-3"></i> Für den Belag Aprikosenmarmelade mit einem Löffel auf dem Teig verteilen und glattstreichen. Butter, Zucker und Vanillezucker in einem Topf schmelzen. Gemahlene Nüsse und 4 EL Wasser hinzugeben und unter ständigem Rühren kurz köcheln lassen, sodass eine feuchte Masse entsteht. Nussmasse auf dem Blech verteilen und ebenfalls glatt streichen. Nussecken im vorgeheizten Ofen circa 25-27 Minuten backen. Kurz abkühlen lassen. Den noch warmen Kuchen in Ecken schneiden. Dazu in Quadrate schneiden und diese dann über die Ecken halbieren. Ecken vollständig auskühlen lassen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-4"></i> Zartbitterkuvertüre grob hacken und im Wasserbad schmelzen. In ein kleines, hohes Gefäß umfüllen. Nussecken aus der Form neben und die spitzen Ecken jeweils in die Schokolade tunken. Ecken dann auf ein Backpapier legen und die Schokolade trocknen lassen.
              </div>
            </el-card>
          </span>
        </el-dialog>
        <!--R45-->
        <el-dialog :visible.sync="Dialog_R45">
          <span>
            <div class="heading_title">
              CRUMBLE
            </div>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont shucai"></i>Zutaten
            </div>
            <el-card
              class="card_tag"
              shadow="never"
            >90 g Haferflocken
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >120 g brauner Zucker
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >35 g Mehl
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >3 EL kalte Butter
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >600 g Obst ( gemischte Beeren, Äpfel...), frisch oder tiefgefroren
            </el-card>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont cook"></i>Zubereitung
            </div>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-1"></i> Den Ofen auf 180 ° C vorheizen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-2"></i> Die Haferflocken, den braunen Zucker, das Mehl und die Butter vermengen bis sie gleichmäßige Streusel bilden.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-3"></i> Die Früchte in einer feuerfesten Glas- oder Keramikschale von 20x20cm Seitenlänge verteilen und mit der Mischung bedecken.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-4"></i> Ohne Deckel ca. 30-45 Minuten im Ofen backen bis die Früchte kochen und in der Mitte ganz gar sind. Lauwarm servieren.
              </div>
            </el-card>
          </span>
        </el-dialog>
        <!--R46-->
        <el-dialog :visible.sync="Dialog_R46">
          <span>
            <div class="heading_title">
              Pasta mit grünem Spargel und Pistazien in Zitronen-Buttersoße
            </div>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont shucai"></i>Zutaten
            </div>
            <div class="heading_subtitle">PORTIONEN 4 Personen
            </div>
            <el-card
              class="card_tag"
              shadow="never"
            >400 g Pasta nach Wahl - z.B. Penne 8-10 Min. Kochzeit
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >500 g grüner Spargel - gewaschen und in ca. 2-3 cm große Stücke geschnitten
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 Bio-Zitrone - Abrieb
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >50 g Pistazien mit Schale, geröstet, ohne Salz - Aus der Schale nehmen und grob hacken
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >60 g Butter
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >2 Knoblauchzehen - fein geschnitten
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >30 g Basilikum - grob gehackt
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Meersalz, schwarzer Pfeffer und Zitronensaft - zum Abschmecken Parmsan, gerieben - zum Servieren
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Zitronenscheiben und etwas -abrieb - zum Servieren
            </el-card>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont distance"></i>Zeit
            </div>
            <div>
              <el-card
                class="card_time"
                shadow="never"
              >GESAMT 30 Minuten
              </el-card>
            </div>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont cook"></i>Zubereitung
            </div>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-1"></i> In einem sehr großen Topf (in den der Spargel auch noch hinein passt) bringst Du erst einmal das Salzwasser zum kochen, gibst die Pasta hinein und stellst die Kochzeit ein.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-2"></i> Bis das Pasta-Wasser kocht schon einmal die Pistazien aus der Schale lösen und grob hacken sowie den Basilikum klein schneiden. Die Bio-Zitrone heiß abwaschen und die Schale abreiben sowie den Knoblauch schälen und fein schneiden.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-3"></i> Den grünen Spargel waschen, die holzigen Enden (ca. 2 cm) abschneiden und schräg und nach Belieben in ca. 2-3 cm große Stücke schneiden. Der grüne Spargel kommt die letzten 5 Minuten der Nudelkochzeit zu den Nudeln in das Kochwasser.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-4"></i> Die Butter zerlässt Du auf mittlerer Stufe in einer großen Pfanne (Nudeln und Spargel müssen hinein passen) und dünstest den Knoblauch darin an bis er schön duftet. Er darf aber nicht zu dunkel werden sonst wird er bitter.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-5"></i> Nach der Kochzeit die Nudeln mit dem Spargel abgießen und zu der Knoblauch- Butter in die Pfanne geben.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-6"></i> Jetzt gibst Du die restlichen Zutaten hinein: nämlich die gehackten Pistazien, den Zitronenschalenabrieb und das Basilikum. Alles miteinander vermengen und mit Meersalz und schwarzem Pfeffer kräftig würzen sowie mit Zitronensaft abschmecken.Ich wünsche Dir einen guten Appetit!
              </div>
            </el-card>
          </span>
        </el-dialog>
        <!--R47-->
        <el-dialog :visible.sync="Dialog_R47">
