                <i class="iconfont icon-4"></i> zu den warmen Apfelradl schmeckt Vanilleeis hervorragend.
              </div>
            </el-card>
          </span>
        </el-dialog>
        <!--R48-->
        <el-dialog :visible.sync="Dialog_R48">
          <span>
            <div class="heading_title">
              Moqueca Pfanne
            </div>
          </span>
          <span>
            <el-row
              gutter="60"
              type="flex"
              justify="center"
            >
              <el-col span="4">
                <el-avatar
                  :size="120"
                  fit="fit"
                  src="Frank"
                ></el-avatar>
              </el-col>
              <el-col span="20">
                <div
                  class="heading_name"
