                >
                  Frank Störbrauck
                </div>
                <el-card
                  class="card_chat"
                  shadow="never"
                >
                  <div class="text_content">
                    <div>
                      Als ich die letzten beiden Male in Brasilien war, habe ich immer wieder in den Restaurants diesen leckeren brasilianischen Fischeintopf gegessen: Moqueca. Besonders im Nordosten des Landes, in Bahia, ist das Gericht weit verbreitet. Viel Spaß beim Kochen!
                    </div>
                  </div>
                </el-card>
              </el-col>
            </el-row>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont camera"></i>Foto</div>
            <div>
              <el-card>
                <el-image
                  style="width:100%"
                  src="https://ftp.bmp.ovh/imgs/2020/04/f29eff0b276fc8c4.jpeg"
                ></el-image>
              </el-card>
            </div>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont shucai"></i>Zutaten
            </div>
            <el-card
              class="card_tag"
              shadow="never"
            >ca. 600 Gramm Kabeljau, Barsch oder Lachs
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >ca. 200-300 Gramm Krabben
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 Dose zerhackte Tomaten (400 Gramm)
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >3 Zwiebeln
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >2 rote Paprika
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >halber Bund Koreander
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 Dose Kokosmilch (400 ml)
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Limettensaft (ca. 150 ml, frisch gepresst oder fertigen)
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >2 Esslöffel Palmöl
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 Esslöffel Olivenöl
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >4 Zehen Knoblauch
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 Esslöffel Fischsauß
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Salz, Pfeffer, Zucker, scharfer Paprika zum Abschmecken
            </el-card>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont cook"></i>Zubereitung
            </div>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-1"></i> Zunächst macht ihr euch an den Fisch und die Krabben. Diese werden erst einmal aufgetaut und anschließend für 30 Minuten in eine Marinade aus Limettensaft, der Hälfte des Koreanders und zwei ausgepressten Knoblauchzehen gelegt.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-2"></i> Jetzt gehts ans Schnibbeln: Zwiebeln in Würfel und Paprika in Streifen schneiden. Währenddessen könnt ihr schon einmal die Pfanne oder den Kochtopf mit dem Palmöl und Olivenöl heiß machen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-3"></i> Anschließend die übrigen zwei Knoblauchzehen darin auspressen und die klein gehackten Zwiebeln glasig anbraten. Anschließend kommen die Tomatenstücke, und das Paprika hinzu, dann die Kokosmilch und der Fisch sowie die Krabben.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-4"></i> Nun aufkochen lassen, danach auf mittlerer Stufe weiterköcheln, circa 30 Minuten lang.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-5"></i> Wer es gern schärfer mag, kann Chilichoten mitköcheln lassen. Mehr als vier Schoten sollten es aber nicht sein! Zur Moqueca schmeckt wunderbar Reis. Guten Appetit!
              </div>
            </el-card>
          </span>
        </el-dialog>
        <!--R49-->
        <el-dialog :visible.sync="Dialog_R49">
          <span>
            <div class="heading_title">
              Maultaschen
            </div>
          </span>
          <span>
            <el-row
              gutter="60"
              type="flex"
              justify="center"
            >
              <el-col span="4">
                <el-avatar
                  :size="120"
                  fit="fit"
                >
                  <div class="avatar_text">UR</div>
                </el-avatar>
              </el-col>
              <el-col span="20">
                <div
                  class="heading_name"
                  style="margin-bottom:10px"
                >
                  Uschi Rosenbauer
                </div>
                <el-card
                  class="card_chat"
                  shadow="never"
                >
                  <div class="text_content">
                    <div>
                      Hallo Angie,
                    </div>
                    <div>anbei ein Rezept aus meiner Heimat Baden-Württemberg. Für die Maultaschen gibt es viele Rezepte. Meistens werden diese von einer Generation auf die andere weitergegeben.
                    </div>
                    <div>Das ursprüngliche Rezept stammt von meiner Oma. Meine Mama und später dann auch ich, haben einige Änderungen vorgenommen, so dass das Rezept für die heutige Zeit geeignet ist. Viel Spaß beim Nachkochen.
                    </div>
                    <div>Lieben Gruß
                    </div>
                    <div>Uschi Rosenbauer
                    </div>
                  </div>
                </el-card>
              </el-col>
            </el-row>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont shucai"></i>Zutaten
            </div>
            <el-card
              class="card_tag"
              shadow="never"
            >500 g Nudelteig (Schmidt’s No 1 ist für mich der beste)
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >400 g Hackfleisch
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >250 g Bratwurstbrät
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >100 g gerauchte Schinkenwurst (klein würfeln)
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >100 g magerer Bauch kleine Würfel
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 Bund Petersilie
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >2 Bund Lauchzwiebeln
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >250 g gehackten Spinat
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >2 Brötchen (1 Tag alt)
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >4 Eier + 1 Eigelb
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 Eiweiß (zum bestreichen des Teiges)
            </el-card>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont cook"></i>Zubereitung
            </div>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-1"></i> Brötchen einweichen, Lauchzwiebeln und Petersilie kleinhacken und in Butter andünsten, anschließend den aufgetauten Spinat dazugeben.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-2"></i> Die Brötchen ausdrücken und kleinzupfen mit dem Hackfleisch, dem Brät, Schinkenwurst, Bauch, den Eiern und den angedünsteten Lauchzwiebeln, Petersilie und dem Spinat vermengen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-3"></i> Den Fleischteig mit Salz und Pfeffer würzen. Den Fleischteig gleichmäßig auf den mit Eiweiß bestrichenen Nudelteig auf eine Hälfte streichen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-4"></i> Die andere Hälfte darüber klappen und die Ränder gut festdrücken. Darauf achten, dass sich keine Luftblasen bilden.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-5"></i> Mit einem Teigrädchen die Maultaschen in der gewünschten Größe ausrädeln. In sprudelndes Salzwasser geben und 10 Minuten ziehen lassen.
              </div>
            </el-card>
          </span>
        </el-dialog>
        <!--R50-->
        <el-dialog :visible.sync="Dialog_R50">
          <span>
            <div class="heading_title">
              Dampfnudeln
            </div>
          </span>
          <span>
            <el-row
              gutter="60"
              type="flex"
              justify="center"
            >
              <el-col span="4">
                <el-avatar
                  :size="120"
                  fit="fit"
                  src="https://i.postimg.cc/L6FkzJ2Z/image.png"
                ></el-avatar>
              </el-col>
              <el-col span="20">
                <div
                  class="heading_name"
                  style="margin-bottom:10px"
                >
                  Dirk Nowotny
                </div>
                <el-card
                  class="card_chat"
                  shadow="never"
                >
                  <div class="text_content">
                    <div>
                      Guten Appetit.
                    </div>
                  </div>
                </el-card>
              </el-col>
            </el-row>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont shucai"></i>Zutaten
            </div>
            <el-card
              class="card_tag"
              shadow="never"
            >25-30g Hefe
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1/8 Liter lauwarme Milch
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >3 EL Zucker
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >500g Mehl
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >60-80g Butter
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >2-3 Eier
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Salz</el-card>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont cook"></i>Zubereitung
            </div>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-1"></i> Die Hefe mit etwas lauwarmer Milch und einem Teelöffel Zucker auflösen und 10 Minuten ruhen lassen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-2"></i> Dann das Mehl mit der aufgeweichten Butter, den Eiern und der restlichen Milch, Zucker und der angemachten Hefe gut vermengen und den Teig schlagen bis er Blasen wirft und sich von der Schüssel löst.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-3"></i> Den Hefeteig mit einem Tuch zugedeckt an einem warmen Ort gehen lassen. Dann den Teig 1,5 cm dick ausrollen, mit einem Glas ausstechen und nochmals gehen lassen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-4"></i> Eine Pfanne gut ausfetten, 1-2 Tassen Wasser und eine Brise Salz zum kochen bringen und die Nudeln einlegen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-5"></i> Die Pfanne zudecken und bei schwacher Hitze die Nudeln aufziehen lassen.Die Garzeit beträgt 15-20 Minuten. Ist das Wasser verdampft wird der Deckel entfernt, so dass keine Wassertropfen auf die fertigen Dampfnudeln fallen.
              </div>
            </el-card>
          </span>
        </el-dialog>
        <!--R51-->
        <el-dialog :visible.sync="Dialog_R51">
          <span>
            <div class="heading_title">
              Bigos (original polnischer Sauerkrauteintopf)
            </div>
          </span>
          <span>
            <el-row
              gutter="60"
              type="flex"
              justify="center"
            >
              <el-col span="4">
                <el-avatar
                  :size="120"
                  fit="fit"
                >
                  <div class="avatar_text">Sanny</div>
                </el-avatar>
              </el-col>
              <el-col span="20">
                <div
                  class="heading_name"
                  style="margin-bottom:10px"
                >
                  Sanny Wolf
                </div>
                <el-card
                  class="card_chat"
                  shadow="never"
                >
                  <div class="text_content">
                    <div>
                      Hallo liebe Angie, ich möchte dir gerne ein polnisches Rezept Schicken(es muss ja nicht immer deutsche Küche sein :)).Ich sehe dich gerne Spielen ,leider ist es inmoment nicht möglich aber ich glaube fest daran das du nochmals einen Grand Slam Titel gewinnen kannst. Ich freue mich nächtest Jahr auf Stuttgart auf dich und dann würde ich gerne ein Selfie machen lg Sanny aus Braunschweig
                    </div>
                  </div>
                </el-card>
              </el-col>
            </el-row>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont camera"></i>Foto</div>
            <div>
              <el-card>
                <el-image
                  style="width:100%"
                  src="https://ftp.bmp.ovh/imgs/2020/04/2fb65ea11145c2fe.jpg"
                ></el-image>
              </el-card>
            </div>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont shucai"></i>Zutaten
            </div>
            <div class="heading_subtitle">Portionen 10</div>
            <el-card
              class="card_tag"
              shadow="never"
            >400 g Sauerkraut frisches
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >280 g Weißkohl
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >400 g Fleisch Rind, Schwein, Geflügel gemischt
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >120 g Wurst Krakauer, Schlesische Wurst oder Kabanossi
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >0.40 Scheibe geräucherter Speck
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >4 getrocknete Pflaumen
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >2 g getrockenete Pilze
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1.20 Zwiebeln mittelgroß
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >200 ml Wasser
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1/5 Brühwürfel
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >60 ml Rotwein
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >4 Pimentkörner
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >0.80 Lorbeerblätter
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Wacholderbeeren
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >0.80 EL Schweineschmalz
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >0.80 Äpfel
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Tomatenmark</el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Kümmel</el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Majoran</el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Pfeffer</el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Salz</el-card>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont cook"></i>Zubereitung
            </div>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-1"></i> Ein bis zwei Stunden vor dem Beginn des Kochens schon einmal die getrockneten Pilze (am besten Waldpilze) sowie die Trockenpflaumen in jeweils einem eigenen Gefäß einweichen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-2"></i> Zu Beginn das Sauerkraut in ca. 5 cm lange Stücke schneiden und in einem Sieb gut abtropfen lassen. Den Weißkohl in ähnlich große Stücke schneiden.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-3"></i> Das Fleisch in nicht allzu kleine Stücke schneiden (z.B. 1-2 cm), das Schweineschmalz in einem großen Topf erhitzen und das Fleisch anbraten (keine extra Pfanne verwenden, damit der Bratensatz später in das Bigos kommt). Dabei nur so viel Fleisch gleichzeitig in den Topf geben, dass immer alles nebeneinander Platz hat und lieber in mehreren Portionen nacheinander anbraten (damit das Fleisch nicht so viel Wasser zieht).
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-4"></i> Den Speck in Streifen schneiden, die Zwiebeln und Äpfel schälen und würfeln. Ist alles Fleisch angeboten, etwas das Fett entfernen (aber nicht den Bratensatz) und im Bratenfond Speck, Zwiebeln und Äpfel anschwitzen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-5"></i> Die Pilze und Pflaumen aus dem Einweichwasser nehmen, eventuell noch etwas kleinschneiden und dazu geben. Sobald die Zwiebeln glasig sind, kommt das Sauerkraut und der Weißkohl hinzu.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-6"></i> Ist der Weißkohl zusammen gefallen, kommt der Rotwein, Piment, Lorbeerblätter, Wacholderbeeren, Kümmel und der Majoran mit in den Topf. Das Tomatenmark (oder alternativ zwei frische Tomaten) hinzugeben und auch das Einweichwasser der Pilze durch eine Kaffeefilter in den Topf laufen lassen. Mit Salz und Pfeffer würzen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-7"></i> Jetzt beginnt das Kochen: einen Deckel auf den Topf und in den auf 150°C geheizten Ofen stellen. Dort kocht das ganze etwa 2 bis 2,5 Stunden. Zur Sicherheit ab und zu in den Topf schauen, damit unten nichts anbrennt (ggfls. noch etwas Flüssigkeit zugeben). Das Fleisch sollte nach dem Kochen auch ganz weich sein.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-8"></i> Jetzt nochmal ohne Deckel für eine halbe Stunde auf den Herd, damit Flüssigkeit verkochen kann. Jetzt auch Würste in den Topf geben, damit sie heiß werden (evtl. einpicksen, damit das Fett in den Eintopf laufen kann).
              </div>
            </el-card>
          </span>
        </el-dialog>
        <!--R52-->
        <el-dialog :visible.sync="Dialog_R52">
          <span>
            <div class="heading_title">
              Coca Cola Muffins
            </div>
          </span>
          <span>
            <el-row
              gutter="60"
              type="flex"
              justify="center"
            >
              <el-col span="4">
                <el-avatar
                  :size="120"
                  fit="fit"
                >
                  <div class="avatar_text">WG</div>
                </el-avatar>
              </el-col>
              <el-col span="20">
                <div
                  class="heading_name"
                  style="margin-bottom:10px"
                >
                  Wolfgang Greschner
                </div>
                <el-card
                  class="card_chat"
                  shadow="never"
                >
                  <div class="text_content">
                    <div>
                    </div>
                    <div>Liebe Angie, die COCA-COLA Muffins macht meine polnische Schwägerin zu jedem Familienfest. Sie schmecken total lecker vor allem natürlich den Kindern.
                    </div>
                    <div>LIch hoffe ich kann Dich bald wieder live in Stuttgart und Bad Homburg sehen. Bleib gesund!
                    </div>
                    <div>LSchöne Grüße von Ulm, Wolfgang Greschner
                    </div>
                  </div>
                </el-card>
              </el-col>
            </el-row>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont shucai"></i>Zutaten
            </div>
            <div class="heading_subtitle">Für 12 Muffins</div>
            <el-card
              class="card_tag"
              shadow="never"
            >280 g Mehl
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >3 TL Backpulver
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >½ TL Zimtpulver
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >50 g gehackte Walnüsse
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >50 g Schokoraspel
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 Ei
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >150 g Zucker
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >125 g weiche Butter
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >150 ml COCA-COLA
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >100 g Buttermilch
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Papierbackförmchen oder Muffinform
            </el-card>
            <div class="heading_subtitle">Für die Deko</div>
            <el-card
              class="card_tag"
              shadow="never"
            >60 g Puderzucker
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1-2 EL COCA-COLA
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >COLA Fläschchen aus Weingummi
            </el-card>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont distance"></i>Zeit
            </div>
            <div>
              <el-card
                class="card_time"
                shadow="never"
              >Zubereitungszeit: 30 Minuten
              </el-card>
              <el-card
                class="card_time"
                shadow="never"
              >Backzeit: 35 Minuten
              </el-card>
            </div>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont cook"></i>Zubereitung
            </div>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-1"></i> Den Backofen auf 160 Grad vorheizen. Die Papierförmchen in die Mulden der Muffinform oder auf einem Backblech bereitstellen. Das Mehl in eine Schüssel geben und gründlich mit dem Backpulver, dem Zimt, den Nüssen und den Schokoraspeln mischen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-2"></i> Das Ei schaumig schlagen. Den Zucker, die Butter, COCA-COLA und die Buttermilch hinzufügen und gut verrühren.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-3"></i> Nun die Mehlmischung dazugeben. Mit einem Kochlöffel so lange rühren, bis sich alles zu einem Teig gemischt hat.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-4"></i> Die Förmchen mit Teig füllen und im Backofen (Mitte, Umluft 140 Grad) 35 Minuten backen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-5"></i> Die Muffins noch 15 Minuten in der Form lassen, dann herausnehmen und auf dem Kuchengitter abkühlen lassen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-6"></i> Für die Dekoration Puderzucker mit COCA-COLA verrühren und auf die Muffins streichen. Jedes Muffin mit 1-2 Weingummi-Fläschchen dekorieren.
              </div>
            </el-card>
          </span>
        </el-dialog>
        <!--R53-->
        <el-dialog :visible.sync="Dialog_R53">
          <span>
            <div class="heading_title">
              Schmandtorte
            </div>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont shucai"></i>Zutaten
            </div>
            <el-card
              class="card_tag"
