            </div>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-1"></i> Aus den ersten 5 Zutaten einen Teig herstellen und in eine Springform geben, mit einer Gabel mehrmals einstechen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-2"></i> Quark, Schmand, Zucker, Vanillezucker, Eigelb, Sahne, Puddingpulver (nicht vorher kochen), Öl und Milch vermengen und die Creme auf den Teig in die Springform streichen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-3"></i> 30 bis 40 Minuten bei 170 C° backen, bei mittlerer Schiene.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-4"></i> Am Ende 3 Eiweiß mit 3 Esslöffel Zucker steifschlagen und auf den heißen Kuchen geben und nochmal 10 Minuten backen. Guten Appetit.
              </div>
            </el-card>
          </span>
        </el-dialog>
        <!--R54-->
        <el-dialog :visible.sync="Dialog_R54">
