          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont cook"></i>Zubereitung
            </div>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-1"></i> Wasser mit Lorbeerblatt, Wacholderbeeren, Salz, Pfeffer und Zucker in einem Topf aufkochen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-2"></i> Milch zugeben, mit etwas Mehl andicken und nochmals aufkochen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-3"></i> Ofen kleiner drehen und aufpassen, dass es nicht überkocht! (evtl. Kurz vom Herd nehmen).
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-4"></i> Eier in die „Suppe“ schlagen. wenn die Eier langsam fest werden, Eier drehen!
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-5"></i> In einer Pfanne Speck auslassen (Menge etwas nach Gefühl). vorsichtig Speck mit dem Fett zu den Eiern geben!
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-6"></i> vom Ofen nehmen. wenn es nicht mehr kocht (wichtig, sonst schlackert es) Essig zugeben und Alles mit Salz, Pfeffer, Essig und Zucker abschmecken! Man bekommt beim 2-3 Mal ein gute Gefühl dafür... Dazu Salzkartoffeln servieren. Fertig! Viel Spaß beim probieren, ich hoffe es schmeckt!!!
              </div>
            </el-card>
          </span>
        </el-dialog>
        <!--R55-->
