            <div class="heading_step">
              <i class="iconfont shucai"></i>Zutaten
            </div>
            <el-card
              class="card_tag"
              shadow="never"
            >400 g ziemniaków
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >100 g mąki
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >2 łyżki kaszy manny ( może być bez)
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >150 g miękkiego masła
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >2 łyżki cukru
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >szczypta soli
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 żółtko
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >12 dojrzałych śliwek ( mogą być też truskawki)
            </el-card>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont cook"></i>Zubereitung
            </div>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-1"></i>Ziemniaki obrać i ugotować. Odcedzić przecisnąć przez praskę lub ubić tłuczkiem i lekko ostudzić. Dodać mąkę, kaszę, jedną łyżkę masła, dwie łyżki cukru, żółtko i sól. Wszystko wyrobić na gładkie ciasto. Odstawić na 20 minut.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-2"></i>Owoce umyć, ze śliwek wyjąć pestki. Na oproszoną mąką stolnicy lub stole uformować wałek o grubości 5 cm i pokroić go na 12 kawałków. Kawałki lekko spłaszczyć i do każdego włożyć śliwkę lub truskawkę (mozna dodac troche cukru). Owoce dokładnie okleić ciastem. Knedle włożyć do gotującej się osolonej wody i gotować na małym ogniu około 20 minut. Gotowe knedle wyjac i odcedzic.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-3"></i>Resztę masła roztopić i polać nim knedle. Ewentualnie można je posypać cukrem lub polać słodką śmietaną.
              </div>
            </el-card>
          </span>
        </el-dialog>
        <!--R56-->
        <el-dialog :visible.sync="Dialog_R56">
          <span>
            <div class="heading_title">
              Gefüllte Paprikaschoten mit Reis
            </div>
          </span>
          <span>
            <el-row
              gutter="60"
              type="flex"
              justify="center"
            >
              <el-col span="4">
                <el-avatar
                  :size="120"
                  fit="fit"
                  src="https://i.postimg.cc/Kz7m37r5/image.png"
                ></el-avatar>
              </el-col>
              <el-col span="20">
                <div
                  class="heading_name"
                  style="margin-bottom:10px"
                >
                  Samantha Pietzarka
                </div>
                <el-card
                  class="card_chat"
                  shadow="never"
                >
                  <div class="text_content">
                    <div>
                      Liebe Angie,
                    </div>
                    <div>mein Name ist Samantha Pietzarka und ich wohne derzeit in Hamburg. Ich verfolge deine Karriere schon seit einigen Jahren und bin ein großer Fan von dir. Ich bewundere dich als Spielerin sehr, aber vor allem als Menschen Angie. Du bist trotz deines Erfolges immer auf dem Teppich geblieben und hast dich nie unterkriegen lassen, obwohl es Phasen in deinem Leben gab die nicht einfach waren. Aber du hast immer gekämpft und hast nie die Menschen in deinem Leben vergessen. Du hast mir mit deiner Art und mit deinen unermüdlichen Kämpfen auf und neben dem Tennisplatz immer viel gezeigt und zwar vor allem nicht auf zu geben. Du bist jemand der seine eigenen Entscheidungen trifft und das Wichtigste ist dabei, dass du dich dabei wohl und glücklich fühlst. Weist du bis ich auf meine Gefühle hören konnte hat es eine ganze Weile gedauert und ich hatte viele Tiefschläge, aber dann sehe ich Personen wie du die niemals aufgeben und zu dem stehen was sie sagen und das gibt einem dann auch selber die Kraft.
                    </div>
                    <div>Und so viel kann ich nur sagen: solange du dabei glücklich bist bin ich es als Fan mit dir zusammen.
                    </div>
                    <div>Du hast mit diesem Ehrgeiz und einfach weil du auf dem Boden geblieben bist schon so viele Ziele erreicht und hast schon viele Pokale gewonnen und du warst auch die Nummer 1 der Welt. Ich weiß du hattest eine schwierige Phase und Corona macht es momentan alles für uns nicht einfacher, aber jetzt heißt es zusammen halten und aufeinander aufpassen, aber auch jemanden eine kleine Freude zu machen. So wie wir es gerade bei dir hoffentlich machen.
                    </div>
                    <div>Unsere Facebook Gruppe besser unser Leiter Hr. Jörgen Nissen hat den tollen Vorschlag gemacht dir ein Kochbuch zu erstellen, da du ja wie wir gehört haben die freie Zeit fürs Kochen nutzt. Und ich finde diese Idee sehr gut. Vor allem da ich, wenn ich die Möglichkeit habe, es auch probiere neben dem alltäglichen Stress.
                    </div>
                    <div>Ich hoffe sehr für dich, dass du für dich schöne viele tolle Ideen findest und dich das auch einfach ablenkt von der allgemeinen Situation. Meine Ablenkung ist derzeit mein Umzug und die dazugehörige Planung. Alles nicht so einfach, aber man muss alles Stück für Stück machen und dann auf das Beste hoffen. Übrigens ich hätte dich sehr gerne in Berlin gesehen und die Chance gehabt dich vielleicht auch live zu treffen. Hatte mir auch eine Karte fürs ganze Turnier gekauft. Mal sehen vielleicht wird es ja etwas im neuen Jahr. Sofern du dann nach Berlin kommst und wir alle die Corona Krise überstanden haben. Würde mich sehr darüber freuen.
                    </div>
                    <div>Das waren jetzt ein paar Zeilen von mir und bitte bleibe gesund und einfach so wie du bist.
                    </div>
                    <div>Pass gut auf dich und deine Familie auf und genieße diese Zeit zum Kraft tanken und einfach für dich sein. Und sobald es wieder los geht werden wir dich unterstützen und dann neue schöne Erlebnisse zusammen haben und vielleicht sehen wir uns dann ja auch mal von Angesicht zu Angesicht.
                    </div>
                    <div>Ganz liebe Grüße
                    </div>
                    <div>Samantha Pietzarka
                    </div>
                  </div>
                </el-card>
              </el-col>
            </el-row>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont camera"></i>Foto</div>
            <div>
              <el-card>
                <el-image
                  style="width:100%"
                  src="https://ftp.bmp.ovh/imgs/2020/04/375e41bc7db8a526.png"
                ></el-image>
              </el-card>
            </div>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont shucai"></i>Zutaten
            </div>
            <div class="heading_subtitle">Für 4 Personen</div>
            <el-card
              class="card_tag"
              shadow="never"
            >4 Paprikaschoten
            </el-card>
            <div class="heading_subtitle">Für die Füllung</div>
            <el-card
              class="card_tag"
              shadow="never"
            >350 g gemischtes Hackfleisch
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 altbackenes Brötchen
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 kleine Zwiebel
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 Knoblauchzehe
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 TL Paprikapulver edelsüß
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 Ei
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 TL Salz
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Pfeffer</el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Petersilie</el-card>
            <div class="heading_subtitle">Für die Soße</div>
            <el-card
              class="card_tag"
              shadow="never"
            >1 kleine Zwiebel
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 Knoblauchzehe
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >2 Tomaten
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >300 ml passierte Tomaten
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 EL Öl
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >1 TL Oregano
            </el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Salz</el-card>
            <el-card
              class="card_tag"
              shadow="never"
            >Pfeffer</el-card>
          </span>
          <el-divider></el-divider>
          <span>
            <div class="heading_step">
              <i class="iconfont cook"></i>Zubereitung
            </div>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-1"></i>Die Paprikaschoten waschen und am Stielende vorsichtig einen Deckel abschneiden. Die Kerne, sowie die weißen Scheidewände mit einem kleinen Messer entfernen. Die Schote mit Wasser ausspülen. Die Schotenunterseiten eventuell mit einem großen Messer begradigen, damit diese nachher gerade in der Auflaufform stehen. Dabei darf allerdings kein Loch in der Schotenunterseite entstehen. Die abgetrennten Deckel aufbewahren. Diese werden später für die Soße benötigt. Da die gefüllte Paprika mit Hackfleisch im Backofen zubereitet wird, sollte der Backofen jetzt schon vorgeheizt werden (200 °C).
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
                <i class="iconfont icon-2"></i>Für die Zubereitung der Hackfleischfüllung zuerst das Brötchen in einer kleinen Schüssel mit Wasser einweichen. Die Zwiebel, sowie den Knoblauch fein würfeln bzw. den Knoblauch durch eine Knoblauchpresse drücken und die Petersilie fein hacken. Das Hackfleisch, das Ei, die Zwiebelwürfel, die Petersilie und die zerkleinerte Knoblauchzehe in eine Schüssel geben. Das eingeweichte Brötchen gut ausdrücken und ebenfalls hinzufügen.
              </div>
            </el-card>
            <el-card
              shadow="never"
              class="card_step"
            >
              <div>
