d bin ein großer Fan von dir. Ich bewundere dich als Spielerin sehr, aber vor allem als Menschen Angie. Du bist trotz deines Erfolges immer auf dem Teppich geblieben und hast dich nie unterkriegen lassen, obwohl es Phasen in deinem Leben gab die nicht einfach waren. Aber du hast immer gekämpft und hast nie die Menschen in deinem Leben vergessen. Du hast mir mit deiner Art und mit deinen unermüdlichen Kämpfen auf und neben dem Tennisplatz immer viel gezeigt und zwar vor allem nicht auf zu geben. Du bist jemand der seine eigenen Entscheidungen trifft und das Wichtigste ist dabei, dass du dich dabei wohl und glücklich fühlst. Weist du bis ich auf meine Gefühle hören konnte hat es eine ganze Weile gedauert und ich hatte viele Tiefschläge, aber dann sehe ich Personen wie du die niemals aufgeben und zu dem stehen was sie sagen und das gibt einem dann auch selber die Kraft.\n                    ")]),_vm._v(" "),_c('div',[_vm._v("Und so viel kann ich nur sagen: solange du dabei glücklich bist bin ich es als Fan mit dir zusammen.\n                    ")]),_vm._v(" "),_c('div',[_vm._v("Du hast mit diesem Ehrgeiz und einfach weil du auf dem Boden geblieben bist schon so viele Ziele erreicht und hast schon viele Pokale gewonnen und du warst auch die Nummer 1 der Welt. Ich weiß du hattest eine schwierige Phase und Corona macht es momentan alles für uns nicht einfacher, aber jetzt heißt es zusammen halten und aufeinander aufpassen, aber auch jemanden eine kleine Freude zu machen. So wie wir es gerade bei dir hoffentlich machen.\n                    ")]),_vm._v(" "),_c('div',[_vm._v("Unsere Facebook Gruppe besser unser Leiter Hr. Jörgen Nissen hat den tollen Vorschlag gemacht dir ein Kochbuch zu erstellen, da du ja wie wir gehört haben die freie Zeit fürs Kochen nutzt. Und ich finde diese Idee sehr gut. Vor allem da ich, wenn ich die Möglichkeit habe, es auch probiere neben dem alltäglichen Stress.\n                    ")]),_vm._v(" "),_c('div',[_vm._v("Ich hoffe sehr für dich, dass du für dich schöne viele tolle Ideen findest und dich das auch einfach ablenkt von der allgemeinen Situation. Meine Ablenkung ist derzeit mein Umzug und die dazugehörige Planung. Alles nicht so einfach, aber man muss alles Stück für Stück machen und dann auf das Beste hoffen. Übrigens ich hätte dich sehr gerne in Berlin gesehen und die Chance gehabt dich vielleicht auch live zu treffen. Hatte mir auch eine Karte fürs ganze Turnier gekauft. Mal sehen vielleicht wird es ja etwas im neuen Jahr. Sofern du dann nach Berlin kommst und wir alle die Corona Krise überstanden haben. Würde mich sehr darüber freuen.\n                    ")]),_vm._v(" "),_c('div',[_vm._v("Das waren jetzt ein paar Zeilen von mir und bitte bleibe gesund und einfach so wie du bist.\n                    ")]),_vm._v(" "),_c('div',[_vm._v("Pass gut auf dich und deine Familie auf und genieße diese Zeit zum Kraft tanken und einfach für dich sein. Und sobald es wieder los geht werden wir dich unterstützen und dann neue schöne Erlebnisse zusammen haben und vielleicht sehen wir uns dann ja auch mal von Angesicht zu Angesicht.\n                    ")]),_vm._v(" "),_c('div',[_vm._v("Ganz liebe Grüße\n                    ")]),_vm._v(" "),_c('div',[_vm._v("Samantha Pietzarka\n                    ")])])])],1)],1)],1),_vm._v(" "),_c('el-divider'),_vm._v(" "),_c('span',[_c('div',{staticClass:"heading_step"},[_c('i',{staticClass:"iconfont camera"}),_vm._v("Foto")]),_vm._v(" "),_c('div',[_c('el-card',[_c('el-image',{staticStyle:{"width":"100%"},attrs:{"src":"https://ftp.bmp.ovh/imgs/2020/04/375e41bc7db8a526.png"}})],1)],1)]),_vm._v(" "),_c('el-divider'),_vm._v(" "),_c('span',[_c('div',{staticClass:"heading_step"},[_c('i',{staticClass:"iconfont shucai"}),_vm._v("Zutaten\n            ")]),_vm._v(" "),_c('div',{staticClass:"heading_subtitle"},[_vm._v("Für 4 Personen")]),_vm._v(" "),_c('el-card',{staticClass:"card_tag",attrs:{"shadow":"never"}},[_vm._v("4 Paprikaschoten\n            ")]),_vm._v(" "),_c('div',{static